.firmware-upgrade-modal {
  @extend %Hpe_Layer_Style_01;
  height: 260px;
  width: 330px;
  position: fixed;
  z-index: 1040;
  left: 0;
  top: 30%;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column;

  .modal-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 7px 20px 7px 20px;
    border-bottom: 1px solid $HpeColorGrey_10;

    h3 {
      @extend %Hpe_Font_Style_03_05;
    }

    .close-modal {
      display: none;
    }
  }

  .modal-content {
    display: flex;
    flex: 1;
    flex-direction: column;

    .firmware-upgrade-content {
      flex: 1;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      .firmware-upgrade-info {
        height: 180px;
        padding: 20px 20px 20px 20px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        overflow: auto;

        .outdated,
        .updated {
          ul {
            padding: 0 0 0 24px;

            li {
              @extend %Hpe_Font_Style_03_05;
              margin-bottom: 4px;
              list-style-type: disc;
            }
          }
        }

        .outdated {
          margin-bottom: 12px;

          h2 {
            @extend %Hpe_Font_Style_03_05;
            margin-bottom: 12px;
          }
        }

        .updated {
          margin-bottom: 36px;

          h2 {
            @extend %Hpe_Font_Style_13_05;
            margin-bottom: 12px;
          }
        }

        h3 {
          @extend %Hpe_Font_Style_03_05;
        }
      }

      .firmware-upgrade-footer {
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        padding: 7px 20px 7px 20px;
        height: 47px;
        border-top: 1px solid $HpeColorGrey_10;

        button {
          text-align: center;
          cursor: pointer;
          text-transform: capitalize;
          color: white;

          &.later {
            @extend %Button_style_02;
          }

          &.download {
            @extend %Button_style_01;
            padding: 0;
          }
        }
      }
    }
  }
}
