.printheads-component {
  position: relative;
  display: flex;
  justify-content: center;

  .small {
    height: 100%;
    max-width: 72px;

    h3 {
      @extend %Hpe_Font_Style_01_12;
      text-align: center;
      text-transform: uppercase;
      height: 26px;
    }

    .printhead-placeholder {
      width: 48px;
      height: 48px;
      margin: auto;
      position: relative;

      .icon-advice {
        height: 20px;
        width: 20px;
        position: absolute;
        right: 0;
        top: 0;
      }
    }
  }

  .large {
    width: 100%;
    height: 172px;

    h3 {
      @extend %Hpe_Font_Style_01_12;
      text-align: center;
      text-transform: uppercase;
      margin-bottom: 16px;
    }

    .printheads-container {
      display: flex;
      flex-direction: row;
      justify-content: space-around;
      align-items: center;
      width: 120px;
      height: 144px;
      margin: 0 auto;

      .printheads-row {
        flex: 1;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 100%;

        &:nth-child(2) {
          justify-content: center;
        }

        .printheads-col {
          margin-left: 14px;
          margin-right: 14px;

          .printhead-placeholder {
            margin: auto;
            width: 28px;
            height: 60px;
            position: relative;

            span {
              margin: 0 auto;
              margin-top: 8px;
              @extend %Hpe_Font_Style_02_03;
              @extend %Paragraph_Style_14;
            }

            .icon-advice {
              position: absolute;
              height: 24px;
              width: 24px;
              right: 0;
              left: 0;
              top: 0;
              bottom: 0;
              margin-left: auto;
              margin-right: auto;
              margin-top: auto;
              margin-bottom: auto;
            }
          }
        }
      }
    }
  }
}
