roll-over-component {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  z-index: 3;
  height: 100%;
  width: 100%;
  top: 0;

  .roll-over-container {
    width: 100%;
    height: 100%;

    .roll-over-display {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      box-sizing: border-box;
      height: 100%;
    }

    .roll-over-list {
      opacity:0;
      transition: opacity ease-in-out 0.2s;
      display: none;
      height: 100%;
      width: 100%;
      position: absolute;
      top: 0;

      @media screen and (max-width: 202px) {
        position: fixed;
        height: 100%;
        width: 100%;
        left: -6px;
        transition: height 250ms;
      }


      .arrow {
      width: 0;
      height: 0;
      border-left: 7px solid transparent;
      border-right: 7px solid transparent;
      border-bottom: 7px solid #B9B8BB;
      position: absolute;
      top: 39px;
      left:44px;
      }

      .arrow:before {
        bottom: 100%;
        border: solid transparent;
        content: " ";
        height: 0;
        width: 0;
        position: absolute;
        pointer-events: none;
        border-color: rgba(221, 221, 221, 0);
        border-bottom-color: white;
        border-width: 8px;
        margin-left: -8px;
        right:-8px;
        top: -7px;
        z-index: 9999;

      }


      .container {
        position: absolute;
        width: 144px;
        z-index: 2;
        cursor: default;
        background: white;
        top: 44px;
        left: -21px;
        padding: 8px 12px 8px 12px;

        @extend %Hpe_Layer_Style_01;

        @media screen and (max-width: 202px) {
          width: 188px;
        }

        > div {
          h2 {
            max-height: 30px;
            max-width: 120px;
            @extend %Hpe_Font_Style_12_05;
            margin-bottom: 8px;
          }

          .unit-info-status {
            display: flex;
            flex-direction: column;

            div {
              margin-bottom: 8px;
              display: flex;

              i {
                width: 12px;
                height: 12px;
              }

              h5 {
                @extend %Hpe_Font_Style_02_05;
                margin-left: 4px;

                &.material-name,
                &.percentage-job {
                  margin-right: 12px;
                  width: 100%;
                  text-overflow: ellipsis;
                  white-space: nowrap;
                  overflow: hidden;
                }
              }
            }
          }
        }
      }
    }
  }

  &:hover  {
    .roll-over-list {
      opacity:1;
      display: flex;
      transition:none;
    }
  }
}
