.isometric-view-component {
  width: 100%;
  height: 100%;

  .widget {
    width: 100%;
    height: 100%;

    .isometric-view-placeholder {
      width: 100%;
      height: 100%;
      position: relative;

      img {
        width: 100%;
        height: 100%;
      }

      .roll-over {
        position: absolute;
        bottom: 0;
        height: 32px;
        width: 100%;
        display: flex;
        align-self: flex-end;
        justify-content: center;
        background: $HpeColorWhite_01;
        opacity: 0.95;
        z-index: 1;
        @media screen and (max-width: 202px) {
          display: none;
        }
        .roll {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 48px;
          height: 100%;
          cursor: pointer;

          i {
            width: 24px;
            height: 24px;
          }
        }
      }

      .roll-over-footer {
        position: absolute;
        bottom: -2px;;
        height: 2px;
        width: 100%;
        display: flex;
        align-self: flex-end;
        justify-content: center;
        background: $HpeColorGrey_12;

        .roll {
          width: 48px;
          height: 100%;
          background-color: $HpeColorBlue_02;
        }
      }

      .no-preview {
        width: 100%;
        position: absolute;
        bottom: 26px;
        text-align: center;
        @extend %Hpe_Font_Style_01_05;
      }

      .no-preview-footer {
        height: 26px;
        width: 100%;
        position: absolute;
        bottom: 0;
      }

    }
  }

