

/**
* Buttons for aplication for job card-container
*
* We are creating a presentation class for the build unit into job card printing
* and printed status
*/
%Button_base {
  height: 32px;
  width: 140px;
  max-width: 170px;
  padding:7px 20px;
  border: none;
}

%Button_style_01 {
  @extend %Button_base;
  @extend  %Hpe_Font_Style_03_03;
  background-color: $HpeColorBlue_03;

  &:hover {
    background-color: $HpeColorBlue_02;
  }

  &:active {
    background-color: $HpeColorBlue_01;
  }

  &:disabled {
    @extend  %Hpe_Font_Style_03_02;
    background-color: $HpeColorGrey_10;
  }
}

%Button_style_02 {
  @extend %Button_base;
  @extend  %Hpe_Font_Style_03_03;
  background-color: $HpeColorGrey_08;

  &:hover {
    background-color: $HpeColorGrey_05;
  }

  &:active {
    background-color: $HpeColorGrey_04;
  }

  &:disabled {
    @extend  %Hpe_Font_Style_03_02;
    background-color: $HpeColorGrey_10;
  }
}
