.job-status-component {
  height: 32px;
  width: 100%;

  &.widget {
    position: absolute;
    width: 160px;
    z-index: 1;

    .printing {
      background-color: $HpeColorGreen_05;
      display: flex;
      height: 100%;
      align-items: center;

      i {
        width: 20px;
        height: 20px;
        margin-right: 4px;
      }

      h3 {
        @extend %Hpe_Font_Style_03_05;
      }
    }
    .printed {
      background-color: $HpeColorGreen_05;
      display: flex;
      width: 160px;
      height: 32px;
      align-items: center;

      i {
        width: 20px;
        height: 20px;
        margin-right: 4px;
        margin-left: 4px;
      }

      h3 {
        @extend %Hpe_Font_Style_03_05;
      }
    }
  }

  &.small {
    position: absolute;
    width: 224px;
    z-index: 1;

    .printing {
      background-color: $HpeColorGreen_05;
      display: flex;
      height: 100%;
      align-items: center;

      i {
        width: 20px;
        height: 20px;
        margin-right: 4px;
      }

      h3 {
        @extend %Hpe_Font_Style_03_05;
      }
    }
    .printed {
      background-color: $HpeColorGreen_05;
      display: flex;
      width: 224px;
      height: 32px;
      align-items: center;

      i {
        width: 20px;
        height: 20px;
        margin-right: 4px;
        margin-left: 4px;
      }

      h3 {
        @extend %Hpe_Font_Style_03_05;
      }
    }
  }

  &.large {
    height: inherit;
    .printing {
      display: flex;
      width: 533px;
      align-items: center;

      i {
        display: none;
        width: 20px;
        height: 20px;
        margin-right: 4px;
      }

      h3 {
        @extend %Hpe_Font_Style_03_05;
      }
    }

    .printed {
      background-color: $HpeColorGreen_05;
      display: flex;
      width: 533px;
      height: 32px;
      align-items: center;

      i {
        width: 20px;
        height: 20px;
        margin-right: 4px;
        margin-left: 4px;
      }

      h3 {
        @extend %Hpe_Font_Style_03_05;
      }
    }

  }
}
