.recent-history-component {

  .small {
    width: 96px;
    height: 100%;

    h3 {
      @extend %Hpe_Font_Style_01_12;
      text-transform: uppercase;
      margin-bottom: 8px;
    }

    .printed-jobs-list {
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
      position: relative;

      .printed-job-placeholders {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
      }

      .printed-job {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        height: 100%;
        width: 100%;
        position: absolute;
        z-index: 1;
        left: 0;
      }

      .printed-job-container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        width: 96px;
        height: 72px;

        .printed-job-placeholder {
          width: 100%;
          height: 100%;

          &.empty {
            background-color: $HpeColorGrey_14;
          }

          .view-jobs-medium-thumbnail {
            padding-top: 4px;
            padding-right: 4px;
            position: relative;

            .icon-status-printed {
              width: 20px;
              height: 20px;
              position: absolute;
              right: 3px;
            }
          }
        }
      }
    }
  }

  .large {
    width: 120px;
    height: 100%;

    h3 {
      @extend %Hpe_Font_Style_01_12;
      text-transform: uppercase;
      margin-bottom: 18px;
    }

    .printed-jobs-list {
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
      position: relative;

      .printed-job-placeholders {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;

        h2 {
          opacity: 0.5;
        }
      }

      .printed-job {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;  
        width: 100%;
        position: absolute;
        z-index: 1;
        left: 0;

        h2 {
          background-color: white;
        }
      }

      .printed-job-container {
        display: flex;
        flex-direction: column;
        width: 120px;
        height: 141px;

        h2 {
          @extend %Hpe_Font_Style_02_05;
          width: 120px;
          text-overflow: ellipsis;
          white-space: nowrap;
          overflow: hidden;
          margin-bottom: 4px;
        }

        .printed-job-placeholder {
          width: 100%;
          height: 90px;

          &.empty {
            background-color: $HpeColorGrey_14;
          }

          .view-jobs-medium-thumbnail {
            padding-top: 4px;
            padding-right: 4px;
            position: relative;

            .icon-status-printed {
              width: 20px;
              height: 20px;
              position: absolute;
              right: 3px;
            }
          }
        }
      }
    }
  }
}
