/* general styles definition for card component */

.card {
  display: flex;
  flex-direction: column;
  position: relative;
  background: white;
  border-radius: 2px;
  
  margin-bottom: 12px;
  margin-top: 12px;
  margin-left: 12px;
  margin-right: 12px;

  @media screen and (max-width: 541px){
    &:not(:first-child) {
      margin-top:0px;
    }
  }

  &.online-card {
    box-shadow: 0px 2px 3px rgba(12, 12, 12, 0.2);
  }

  .offline-card-layer {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: $HpeColorGrey_15;
    z-index: 3;
    opacity: 0.6;

  }

  .critical-error {
    position: absolute;
    z-index: 2;
    left: 0;
    right: 0;
  }

  &.small {
    .critical-error-layer {
      width: 100%;
      height: 200px;
      bottom: 0;
      background: $HpeColorWhite_01;
      opacity: 0.9;
    }

    .critical-error-icon {
      width: 72px;
      height: 72px;
      bottom: 0;
      margin: 0 auto;
      top: 86px;
    }

    .critical-error-text {
      margin: 0 auto;
      bottom: 54px;
      padding: 0 20px 0 20px;
      text-align: center;

      p {
        @extend %Hpe_Font_Style_05_05;
        @extend %Paragraph_Style_20;
        text-transform: none;
      }
    }
    width: $card-width;
    height: 264px;
    padding: 16px 20px 20px 20px;

    .card-status-bar {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;

      .card-status-bar-container {
        width: 100%;
        left: 0;
        height: 4px;
        top: 0;
        border-radius: 0;
        margin: 0;

        &.error {
          background-color: $HpeColorRed_02;
        }

        &.warning {
          background-color: $HpeColorYellow_02;
        }
      }
    }

    .card-component-header {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      margin-bottom: 8px;
      height: 34px;

      .card-component-header-printer {
        width: 45px;
        height: 30px;
        position: relative;

        .icon-status {
          right: -10px;
          top: -9px;
          position: absolute;
          width: 20px;
          height: 20px;
        }
      }

      .card-component-header-info {
        padding-left: 12px;
        max-width: 224px;
        flex-basis: 224px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        height: 34px;

        h1,
        h2 {
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          width: 100%;
        }

        h1 {
          @extend %Hpe_Font_Style_03_05;
          @extend %Paragraph_Style_13;
          margin-top: -1px;
          text-transform: none;
        }

        h2 {
          @extend %Hpe_Font_Style_02_12;
          @extend %Paragraph_Style_25;
        }
      }

      .card-component-header-date {
        padding-right: 12px;
        text-align: right;
        text-transform: capitalize;
        max-width: 154px;
        flex-basis: 154px;
        display: flex;
        flex-direction: column;
        justify-content: center;

        h3 {
          @extend %Hpe_Font_Style_03_05;
        }
      }

      .card-component-header-more {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        cursor: pointer;
      }
    }

    .card-progress-bar {
      position: relative;
      height: 1px;
      background-color: #D5D5D5;
      display: flex;
      overflow: hidden;
    }

  }//end small card style

  &.large {
    height: 532px;
    width: 892px;
    padding: 16px 24px 24px 24px;
    .critical-error-layer {
      width: 100%;
      bottom: 0;
      background: $HpeColorWhite_01;
      opacity: 0.9;
    }

    .critical-error-icon {
      width: 72px;
      height: 72px;
      bottom: 0;
      margin: 0 auto;
      top: 200px;
    }

    .critical-error-text {
      margin: 0 auto;
      bottom: 230px;
      padding: 0 20px 0 20px;
      text-align: center;

      p {
        @extend %Hpe_Font_Style_05_05;
        @extend %Paragraph_Style_20;
        text-transform: none;
      }
    }

    .card-status-bar {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;

      .card-status-bar-container {
        width: 100%;
        left: 0;
        height: 4px;
        top: 0;
        border-radius: 0;
        margin: 0;

        &.error {
          background-color: $HpeColorRed_02;
        }

        &.warning {
          background-color: $HpeColorYellow_02;
        }
      }
    }

    .card-component-header {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      margin-bottom: 14px;
      height: 34px;

      .card-component-header-printer {
        width: 45px;
        height: 30px;
        position: relative;

        .icon-status {
          right: -10px;
          top: -9px;
          position: absolute;
          width: 20px;
          height: 20px;
        }
      }

      .card-component-header-info {
        padding-left: 12px;
        max-width: 514px;
        flex-basis: 514px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        height: 34px;

        h1,
        h2 {
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          width: 100%;
        }

        h1 {
          @extend %Hpe_Font_Style_03_05;
          @extend %Paragraph_Style_13;
          margin-top: -1px;
          text-transform: none;
        }

        h2 {
          @extend %Hpe_Font_Style_02_12;
          @extend %Paragraph_Style_25;
        }
      }

      .card-component-header-date {
        padding-right: 12px;
        text-align: right;
        text-transform: capitalize;
        max-width: 266px;
        flex-basis: 266px;

        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        align-items: center;

        h3 {
          @extend %Hpe_Font_Style_03_05;
        }

        h3:first-child {
          margin-right: 6px;
        }
      }

      .card-component-header-more {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        cursor: pointer;
      }
    }

    .card-component-content {
      display: flex;
      flex-direction: row;
      margin-top: 24px;
      flex: 1;
    }
  } //end large card style
}
