/**
* @desc         Main styles file.
* @name         _basic.scss
* @author       bismark <bismark@blended.io>
* @tested       chrome
* @requires     neat
*/

html, body {
  height: 100%;

  // TODO: add horizontal scroll

  ::-webkit-scrollbar {
    width: 10px;
    background-color: $HpeColorGrey_15;
  }

  ::-webkit-scrollbar-thumb {
    background-color: $HpeColorGrey_10;
    border: 1px solid $HpeColorGrey_12;
    border-radius: 10px;
  }

  ::-webkit-scrollbar-thumb:hover {
    background-color: #aaa;
  }
}

body {
  overflow: hidden;
  height: 100%;
  width: 100%;

  .app {
    height: 100%;
    width: 100%;
    background-color: $HpeColorGrey_13;
    .subheader {
      align-items: center;
      background-color: $HpeColorGrey_15;
      display: flex;
      flex-direction: row;
      height: 36px;
      width: 100%;
      position: absolute;
      z-index: 1;
      top: 48px;

      .settings-container {
        height: 100%;
        display: flex;
        flex-direction: row;
        align-items: center;

        .update-window-button {
          padding: 0 6px 0 6px;
          height: 100%;
          display: flex;
          align-items: center;

          &:hover {
            background: $HpeColorGrey_14;
          }

          &:active {
            background: $HpeColorGrey_13;
          }

          &:visited {
            background: $HpeColorGrey_12;
          }

          @media screen and (max-width: 541px){
            display: none;
          }

          height: 36px;
          width: 36px;
          display: flex;
          align-items: center;
          justify-content: center;

          i {
            cursor: pointer;
            height: 24px;
            width: 24px;
          }
        }

        .default-window-button {
          padding: 0 6px 0 6px;
          height: 100%;
          display: flex;
          align-items: center;

          &:hover {
            background: $HpeColorGrey_14;
          }

          &:active {
            background: $HpeColorGrey_13;
          }

          &:visited {
            background: $HpeColorGrey_12;
          }

          @media screen and (max-width: 541px){
            margin-right: 0;
            margin-left: 12px;
          }
        };

        .settings-window-button {
          padding: 0 6px 0 6px;
          height: 100%;
          display: flex;
          align-items: center;

          &:hover {
            background: $HpeColorGrey_14;
          }

          &:active {
            background: $HpeColorGrey_13;
          }

          &:visited {
            background: $HpeColorGrey_12;
          }

          @media screen and (max-width: 541px){
            margin-right: 0;
            margin-right: 12px;
            margin-left: 0px;

          }
        }
      }
    }

    .base {
      width: 100%;
      height: 100%;
      padding-top: 84px;
      position: absolute;
      display: flex;
      justify-content: center;
      background-color: $HpeColorGrey_13;

      @media screen and (max-width: 892px){
        padding-left: 8px;
        padding-right: 0px;
      }
      .scroll{
        overflow: auto;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        padding-left: 24px;
        padding-right: 24px;
        background: $HpeColorGrey_13;
        @media screen and (max-width: 892px){
          justify-content: flex-start;
          padding-left: 0;
          padding-right: 0;
          .cards-container.large {
            justify-content: flex-start;
            width: 901px;
          }
        }
        @media screen and (max-width: 541px){
          justify-content: flex-start;
          padding-left: 0;
          padding-right: 0;
          .cards-container {
            justify-content: flex-start;
            width: 195px;
          }
        }
      }
    }
  }
}


//scroll bar

html ::-webkit-scrollbar, body ::-webkit-scrollbar {
    width: 12px;
    height:12px;
    background-color: #F4F4F4;
}

html ::-webkit-scrollbar-thumb, body ::-webkit-scrollbar-thumb {
    background-color: $HpeColorGrey_11;
    border: 3px solid #f4f4f4;
    border-radius: 10px;
}

//hover state
html ::-webkit-scrollbar-thumb:hover, body ::-webkit-scrollbar-thumb:hover {
    background-color: $HpeColorGrey_09;
}

//active state
html ::-webkit-scrollbar-thumb:active, body ::-webkit-scrollbar-thumb:active {
    background-color: $HpeColorGrey_08;
}

/**
* Card container for test view
*
* Doing this class we avoid to use inline styles
*/


.cards-container {

 display: flex;
 flex-direction: row;
 flex-wrap: wrap;
 padding: 12px 0 12px 0;

 @media screen and (max-width: 541px){
   justify-content: center;
   padding: 0;
 }

  &.small {
    justify-content: center;

    @media screen and (max-width: 1045px) {
      justify-content: space-around;
    }
  }

  &.large {
    justify-content: center;
    width: 100%;
  }

  &.center {
    justify-content: center;
    &.small.cards-container:after {
      content:'';
      width:0px;
    }

  }

}

//set the last element left aligned

.small.cards-container {
  li.last {
    display: flex;
    height: 0;
    width: $card-width;
    margin-bottom: 12px;
    margin-top: 12px;
    margin-left: 12px;
    margin-right: 12px;
  }
  &.center {
    li.last {
      display: none;
    }
  }
}

.large.cards-container {
  li.last {
    display: flex;
    height: 0;
    width: 892px;
    margin-bottom: 12px;
    margin-top: 12px;
    margin-left: 12px;
    margin-right: 12px;
  }
  &.center {
    li.last {
      display: none;
    }
  }
}



/**
* Build unit for job card-container
*
* We are creating a presentation class for the build unit into job card printing
* and printed status
*/
.workarea {
  width: auto;
  min-width: 1056px;
  max-width: 3168px;
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 0;
  background-color: $HpeColorGrey_13;

  @media screen and (max-width: 1027px){
    .monitor-card-component,
    .job-card-component {
      &.small {
        &:nth-child(odd) {
          margin-right: 0;
        }
      }
    }
  }

  @media screen and (max-width: 1045px) {
    width:1056px;
    min-width: auto;
    .card {
      margin-left:0px;
      margin-right:0px;
    }
  }
}
