dropdown-component {
  display: block;
  position: relative;
  z-index: 2;

  .dropdown-container {
    .dropdown-display {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      width: 24px;
      height: 24px;
      cursor: pointer;
      box-sizing: border-box;

      i {
        z-index: 0;
        width: 24px;
        height: 24px;
      }
    }

    .dropdown-list {
      position: relative;
      opacity:0;
      display: none;
      transition: opacity ease-in-out 0.2s;


      @media screen and (max-width: 202px) {
        position: fixed;
        height: 100%;
        width: 100%;
        left: -6px;
        transition: height 250ms;
      }

      > div {
        position: absolute;
        width: 216px;
        max-width: 288px;
        z-index: 2;
        cursor: pointer;
        background: white;
      	right: -6px;
        top: 6px;
        padding: 12px 0 12px 0;
        border: $HpeColorGrey_12;
        box-shadow: 0 2px 3px rgba(12, 12, 12, 0.20);

        @media screen and (max-width: 202px) {
          width: 188px;
        }
        &:first-child {
          border-right: solid 1px #DDD;
          border-left: solid 1px #DDD;
          border-bottom: solid 1px #DDD;
        }

        > div {
          float: left;
          width: 100%;
          font-size: 12px;
          box-sizing: border-box;
        	margin: 0 auto;

          &.border {
            border: 0;

            &:first-child {
              border-top: solid 1px #DDD;

              :after, :before {
                bottom: 100%;
              	right: -2px;
              	border: solid transparent;
              	content: " ";
              	height: 0;
              	width: 0;
              	position: absolute;
              	pointer-events: none;
              }

              :after {
                border-color: rgba(221, 221, 221, 0);
              	border-bottom-color: white;
              	border-width: 9px;
              	margin-left: -9px;
              }

              :before {
                border-color: rgba(221, 221, 221, 0);
              	border-bottom-color: gray;
              	border-width: 9px;
                margin-left: -9px;
              }
            }
          }

          &:last-child {
            border-bottom: none;
          }

          a {
            @extend %Hpe_Font_Style_02_05;
            @extend %Paragraph_Style_01;
            float: left;
            width: 100%;
            position: relative;
            box-sizing: border-box;
            color: black;
            text-decoration: none;
            margin: 0;
            cursor: pointer;
            text-transform: lowercase;
            height: 40px;
            line-height: 40px !important;
            display: inline-block;

            justify-content: space-between;
            align-items: center;
            padding: 0 20px 0 20px;

            &:first-letter {
              text-transform: uppercase;
            }
            @media screen and (max-width: 202px) {
              font-size: 11px;
            }
          }

          &:hover {
            background-color: $HpeColorGrey_15;
          }

          i {
            width: 12px;
            height: 12px;
            display: inline-block;
            float: right;
            margin-top: 12px;
          }
        }
      }
    }

    &.show  {
      .dropdown-list {
          opacity:1;
          display: block;
          transition:none;
      }
    }
  }
}
