/* specific styles definition for Monitor Cards */

.monitor-card-component {
  &.small {
    .card-progress-bar {
      &.printing {
        height: 3px;

        .WarmingUp {
          width: 10%;
        }

        .Printing {
          width: 90%;
        }

        .Cooling {
          width: 100%;
        }

        :first-child {
          position: absolute;
          height: 100%;
          .fixed-progress {
            width:452px;
            #ngProgress {
              height: 3px;
            }
          }
        }

        :last-child {
          height: 100%;
          width: 100%;
          display: flex;
          z-index: 1;

          div {
            position: relative;
          }

          .warming-up {
            width: 10%;
            border-right: 4px solid white;
          }

          .printing {
            width: 80%;
          }

          .aniling {
            width: 10%;
            border-left: 4px solid white;
          }
        }
      }
    }

    .card-component-content {
      display: flex;
      flex-direction: row;
      margin-top: 12px;
      flex: 1;

      &.is-empty {
        .status-content {
          display: flex;
          flex-direction: row;
          justify-content: center;
          flex: 1;

          .unit {
            width: 116px;
          }

          .info {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            width: 112px;
          }
        }
      }

      &.is-ready {
        .status-content {
          display: flex;
          flex-direction: row;
          justify-content: center;
          flex: 1;

          .unit {
            width: 116px;
          }

          .info {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            width: 112px;
          }
        }
      }

      &.is-printing {
        margin-top: 16px;
        .status-content {
          display: flex;
          flex-direction: row;
          justify-content: center;
          flex: 1;

          .view {
            width: 224px;
            height: 168px;
            margin-right: 16px;
          }

          .unit {
            width: 124px;
          }

          .info {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            width: 88px;
          }
        }
      }

      &.is-printed {
        margin-top: 16px;
        .status-content {
          display: flex;
          flex-direction: row;
          justify-content: center;
          flex: 1;

          .view {
            width: 224px;
            height: 168px;
            margin-right: 16px;
            position: relative;
          }

          .unit {
            width: 124px;
          }

          .info {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            width: 88px;
          }
        }
      }
    }
  }

  &.large {
    .critical-error-layer {
      height:472px;
    }

    .card-progress-bar {
      position: relative;
      height: 1px;
      background-color: #D5D5D5;
      display: flex;
      overflow: hidden;

      &.printing {
        height: 4px;
        width:100%;


        .WarmingUp {
          width: 10%;
        }

        .Printing {
          width: 90%;
        }

        .Cooling {
          width: 100%;
        }

        :first-child {
          position: absolute;
          height: 100%;

          .fixed-progress {
            width:844px;
            #ngProgress {
              height: 4px;
            }
          }
        }

        .card-progress-section {
          height: 100%;
          width: 100%;
          display: flex;
          z-index: 1;

          div {
            position: relative;
          }

          .warming-up {
            width: 10%;
            border-right: 4px solid white;
          }

          .printing {
            width: 80%;
          }

          .aniling {
            width: 10%;
            border-left: 4px solid white;
          }
        }
      }
    }

    .card-component-content {
      &.is-empty {
        height: 100%;
        margin-bottom: 12px;
        .status-content {
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          flex: 1;

          .unit {
            width: 136px;
          }

          .info {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            width: 204px;
          }

          > div:nth-child(2) {
            margin-right: 16px;
          }

          > div:nth-child(4) {
            margin-left: 16px;
          }
        }
      }

      &.is-ready{
        height: 100%;
        .status-content {
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          flex: 1;

          .unit {
            width: 136px;
          }

          .info {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            width: 204px;
          }

          > div:nth-child(2) {
            margin-right: 16px;
          }

          > div:nth-child(4) {
            margin-left: 16px;
          }
        }
      }

      &.is-printing {
        margin-bottom: 0;
        margin-top: 18px;

        .status-content {
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          flex: 1;

          .container {

            display: flex;


            .view {
              width: 533px;
              height: 400px;
              margin-right: 24px;
            }

            .unit {
              display: flex;
              flex-direction: column;
              justify-content: space-between;
              height: 400px;

              &.blocks {
                width: 173px;

                .unit-block {
                  height: 168px;
                  margin-left: 12px;
                  display: flex;
                  flex-direction: column;
                  justify-content: space-between;

                  .unit-sub-block {
                    display: flex;
                    flex-direction: row;
                    justify-content: space-between;
                  }
                }
              }
            }

          }
        }
      }

      &.is-printed {
        margin-bottom: 18px;
        .status-content {
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          flex: 1;

          .container {
            display: flex;
            justify-content: space-between;

            .view {
              width: 533px;
              height: 400px;
              position: relative;
            }

            .unit {
              display: flex;
              flex-direction: column;
              justify-content: space-between;
              height: 400px;

              &.blocks {
                .unit-block {
                  height: 184px;
                  display: flex;
                  flex-direction: column;
                  justify-content: space-between;

                  .unit-sub-block {
                    display: flex;
                    flex-direction: row;
                    justify-content: space-between;
                  }
                }
              }
            }

          }
        }
      }
    }
  }

}
