.confirm-disconnect-modal {
  @extend %Hpe_Layer_Style_01;
  height: 254px;
  width: 330px;
  position: fixed;
  z-index: 1041;
  left: 0;
  top: 25%;
  right: 0;
  margin-left: auto;
  margin-right: auto;

  .modal-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 7px 20px 7px 20px;
    border-bottom: 1px solid $HpeColorGrey_10;
    h3 {
      @extend %Hpe_Font_Style_03_05;
    }
    .close-modal {
      height: 24px;
      width: 24px;
      display: flex;
      cursor: pointer;
    }
  }

  .confirm-disconnect {
    height: 212px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 20px 20px;

    .confirm-disconnect-content {
      height:100%;
      overflow: auto;
      h3 {
        @extend %Hpe_Font_Style_03_05;
        margin-top:4px;
        margin-bottom:16px;
        &.backend {
          @extend %Hpe_Font_Style_13_05;

        }

      }

      p {
        @extend %Hpe_Font_Style_03_05;

      }
    }

    .confirm-disconnect-footer {
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      align-items: center;
      padding: 7px 0px;
      border-top: 1px solid $HpeColorGrey_10;

      a {
        width: 47%;
        text-align: center;
        cursor: pointer;
        // padding: 7px 0px;
      }

      .cancel-button {
        @extend %Button_style_02;
        margin-right: 10px;
        padding: 7px 0px !important;
      }

      .ok-button {
        @extend %Button_style_01;
        padding: 7px 0px !important;
      }
    }
  }
}
