.cip-modal {
  @extend %Hpe_Layer_Style_01;
  height: 352px;
  width: 500px;
  position: fixed;
  z-index: 1040;
  left: 0;
  top: 15%;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column;

  .modal-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 7px 20px 7px 20px;
    border-bottom: 1px solid $HpeColorGrey_10;

    h3 {
      @extend %Hpe_Font_Style_03_05;
    }

    .close-modal {
      height: 24px;
      width: 24px;
      display: flex;
      cursor: pointer;
      align-self: flex-start;
    }
  }

  .modal-content {
    display: flex;
    height: 100%;
    width: 100%;
    flex: 1;

    .cip-content {
      display: flex;
      flex: 1;

      form {
        flex: 1;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        .cip-info {
          flex: 1;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          padding: 20px 20px 20px 20px;

          .cip-info-text {
            flex: 1;
            margin-bottom: 20px;
            overflow: auto;
            padding: 12px 12px 12px 12px;
            border: 1px solid $HpeColorGrey_10;
            border-radius: 2px;

            p {
              @extend %Hpe_Font_Style_02_01;
              text-align: left;
            }
          }

          .cip-info-options {
            .option-group {
              label {
                @extend %Hpe_Font_Style_02_01;
              }

              &:first-child {
                margin-bottom: 12px;
              }
            }

          }
        }

        .cip-footer {
          width: 100%;
          display: flex;
          flex-direction: row;
          justify-content: flex-end;
          align-items: center;
          padding: 7px 20px 7px 20px;
          border-top: 1px solid $HpeColorGrey_10;

          input {
            text-align: center;
            cursor: pointer;
            text-transform: capitalize;
            color: white;

            &.cancel-button {
              @extend %Button_style_02;
              margin-right: 12px;
            }

            &.save-button {
              @extend %Button_style_01;
            }
          }
        }
      }
    }
  }
}
