.application-about-modal {
  @extend %Hpe_Layer_Style_01;
  height: 180px;
  width: 508px;
  position: fixed;
  z-index: 1040;
  left: 0;
  top: 30%;
  right: 0;
  margin-left: auto;
  margin-right: auto;

  .modal-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    .close-modal {
      height: 24px;
      width: 24px;
      display: flex;
      cursor: pointer;
      align-self: flex-start;
    }
  }

  .application-about-component {

    .application-about-content {

      .application-about-company {
        display: flex;
        align-items: center;
        justify-content: space-around;
        height: 96px;
        border-bottom: 1px solid $HpeColorGrey_10;
        padding: 0 36px 0 36px;

        i {
          width: 49px;
          height: 48px;
          margin-right: 12px;
        }

        h1 {
          @extend %Hpe_Font_Style_App_About;
          @extend %Paragraph_Style_01;
          font-weight: normal;
        }
      }

      .application-about-footer {
        height: 58px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 36px 0 36px;

        span {
           @extend %Hpe_Font_Style_02_01;
           @extend %Paragraph_Style_03;
        }
      }
    }
  }
}
