.pending-component {

  .small {
    width: 204px;
    height: 100%;
    margin-right: 28px;
    margin-left: 28px;

    .pending-header {
      display: flex;
      margin-bottom: 8px;
      height:11px;

      h3 {
        @extend %Hpe_Font_Style_01_12;
        text-transform: uppercase;
        margin-right: 8px;
      }

      h4 {
        @extend %Hpe_Font_Style_12_10;
        margin-right: 8px;
        padding-right: 8px;
        line-height: 0.99;
        align-self: flex-end;
        border-right: 1px solid $HpeColorGrey_13;
      }

      .pending-counters {
        width: 100%;
        display: flex;
        justify-content: flex-start;

        .counter {
          display: flex;
          margin-right: 8px;
          height: 12px;
          align-items: center;

          img {
            width: 100%;
            height: 100%;
            margin-right: 4px;
          }

          i {
            width: 12px;
            margin-right: 4px;
          }

          span {
            @extend %Hpe_Font_Style_02_01;
          }
        }
      }
    }

    .pending-jobs-list {
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
      position: relative;

      .pending-job-placeholders {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
      }

      .pending-job {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        height: 100%;
        width: 100%;
        position: absolute;
        z-index: 1;
        left: 0;
      }

      .pending-job-container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        width: 96px;
        height: 72px;
        position: relative;

        .pending-job-placeholder {
          width: 100%;
          height: 100%;

          &.empty {
            background-color: $HpeColorGrey_14;
          }

          .view-jobs-medium-thumbnail {
            padding-top: 4px;
            padding-right: 4px;
            position: relative;

            .icon-job-queue-processed,
            img {
              width: 20px;
              height: 20px;
              position: absolute;
              right: 3px;
            }
          }
        }
      }
    }
  }

  .large {
    height: 100%;
    width: 530px;

    .pending-header {
      display: flex;
      margin-bottom: 16px;

      h3 {
        @extend %Hpe_Font_Style_01_12;
        text-transform: uppercase;
        margin-right: 8px;
      }

      h4 {
        @extend %Hpe_Font_Style_12_10;
        margin-right: 8px;
        padding-right: 8px;
        line-height: 0.99;
        align-self: flex-end;
        border-right: 1px solid $HpeColorGrey_13;
      }

      .pending-counters {
        width: 100%;
        display: flex;
        justify-content: flex-start;

        .counter {
          display: flex;
          margin-right: 8px;
          height: 12px;
          align-items: center;

          img {
            width: 100%;
            height: 100%;
            margin-right: 4px;
          }

          i {
            width: 12px;
            margin-right: 4px;
          }

          span {
            @extend %Hpe_Font_Style_02_01;
          }
        }
      }
    }

    .pending-jobs-list {
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
      position: relative;

      .pending-job-placeholders {
        flex: 1;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;

        h2 {
          opacity: 0.5;
        }
      }

      .pending-job {
        flex: 1;
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        position: absolute;
        left: 0;

        h2 {
          background-color: white;
        }
      }

      .until-6 {
        width: 265px;
        height: 141px;
        justify-content: space-between;

        h2 {
          @extend %Hpe_Font_Style_02_05;
          width: 208px;
          text-overflow: ellipsis;
          white-space: nowrap;
          overflow: hidden;
          margin-bottom: 4px;
        }

        .pending-job-container {
          display: flex;
          flex-direction: row;
          width: 100%;
          height: 90px;
          position: relative;

          .empty {
            background-color: $HpeColorGrey_14;
            width: 120px;
            height: 90px;
          }

          .view-jobs-medium-thumbnail {
            width: 120px;
            height: 90px;
            padding-top: 4px;
            padding-right: 4px;
            position: relative;

            .icon-job-queue-processed,
            img {
              width: 20px;
              height: 20px;
              position: absolute;
              right: 3px;
            }
          }

          .job-info-status {
            margin-left: 8px;
            overflow: hidden;
            width: 115px;

            div {
              display: flex;
              margin-top: 12px;
              align-items: center;

              i {
                width: 16px;
                height: 16px;
                opacity: 0.5;
              }

              h5 {
                margin-left: 4px;
                width: 85%;
                @extend %Hpe_Font_Style_02_05;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
              }
            }
          }
        }
      }

      .more-than-6 {
        width: 120px;
        height: 141px;
        justify-content: flex-start;
        margin-right: 12px;
        h2 {
          @extend %Hpe_Font_Style_02_05;
          width: 120px;
          text-overflow: ellipsis;
          white-space: nowrap;
          overflow: hidden;
          margin-bottom: 4px;
        }

        .pending-job-container {
          display: flex;
          flex-direction: row;
          width: 100%;
          height: 90px;
          position: relative;

          .empty {
            background-color: $HpeColorGrey_14;
            width: 120px;
            height: 90px;
          }

          .view-jobs-medium-thumbnail {
            padding-top: 4px;
            padding-right: 4px;
            position: relative;

            .icon-job-queue-processed,
            img {
              width: 20px;
              height: 20px;
              position: absolute;
              right: 3px;
            }
          }
        }
      }

    }
  }
}
