view-more-component {
  display: block;
  position: relative;
  z-index: 2;

  .view-more-container {
    .dropdown-display {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      width: 24px;
      height: 24px;
      cursor: pointer;
      box-sizing: border-box;

      i {
        z-index: 0;
        width: 24px;
        height: 24px;
      }
    }

    .view-more-list {
      position: relative;
      opacity:0;
      display: none;
      transition: opacity ease-in-out 0.2s;

      @media screen and (max-width: 202px) {
        position: fixed;
        height: 100%;
        width: 100%;
        left: -6px;
        transition: height 250ms;
      }
      .arrow {
      width: 0;
      height: 0;
      border-left: 7px solid transparent;
      border-right: 7px solid transparent;
      border-bottom: 7px solid #B9B8BB;
      left: 5px;
      position: absolute;
      top: -2px;
      left: 5px;
      }

      .arrow:before {
        bottom: 100%;
        border: solid transparent;
        content: " ";
        height: 0;
        width: 0;
        position: absolute;
        pointer-events: none;
        border-color: rgba(221, 221, 221, 0);
      	border-bottom-color: white;
      	border-width: 8px;
        margin-left: -8px;
        right:-8px;
        top: -6px;
        z-index: 9999;

      }

      .container {
        position: absolute;
        width: 216px;
        max-width: 288px;
        cursor: pointer;
        background: white;
      	right: -18px;
        top: 5px;
        padding: 12px 0 12px 0;
        z-index: 9998;

        @extend %Hpe_Layer_Style_01;

        @media screen and (max-width: 202px) {
          width: 188px;
        }

        > div {
          float: left;
          width: 100%;
          font-size: 12px;
          box-sizing: border-box;
        	margin: 0 auto;

          a {
            @extend %Paragraph_Style_01;
            float: left;
            width: 100%;
            position: relative;
            box-sizing: border-box;
            text-decoration: none;
            margin: 0;
            cursor: pointer;
            text-transform: lowercase;
            height: 40px;
            line-height: 40px !important;
            display: inline-block;
            justify-content: space-between;
            align-items: center;
            padding: 0 20px 0 20px;
            &:first-letter {
              text-transform: uppercase;
            }
            @media screen and (max-width: 202px) {
              font-size: 11px;
            }

            span {
              @extend %Hpe_Font_Style_02_01;
            }
          }

          &:hover {
            background-color: $HpeColorBlue_02;

            span {
              @extend %Hpe_Font_Style_02_03;
            }
          }

          &:active {
            background-color: $HpeColorBlue_01;

            span {
              @extend %Hpe_Font_Style_02_03;
            }
          }

          i {
            width: 12px;
            height: 12px;
          }
        }
      }
    }

    &.show  {
      .view-more-list {
        opacity:1;
        display: flex;
        transition:none;
      }
    }
  }
}
