.application-update-available-modal {
  height: 36px;
  width: 100%;
  position: fixed;
  z-index: 1040;
  top: 48px;

  .modal-header {
    display: none;
  }

  .modal-content {
    display: flex;
    height: 100%;
    width: 100%;

    .application-update-available-content {
      flex: 1;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 0 12px 0 16px;

      &.info {
        background-color: $HpeColorBlue_07;
      }

      &.warning {
        background-color: $HpeColorYellow_04;
      }

      &.alert {
        background-color: $HpeColorRed_07;
      }

      i:first-child {
        width: 20px;
        height: 20px;
      }

      i:last-child {
        width: 24px;
        height: 24px;
        cursor: pointer;
        margin-left: 12px;
      }

      .text-notification {
        display: flex;
        align-items: center;
        flex: 1;

        h2 {
          margin: 0 4px 0 4px;
          @extend %Hpe_Font_Style_12_05;
        }

        h3 {
          @extend %Hpe_Font_Style_01_05;
        }
      }

    }
  }
}
