.server-connection-modal {
  @extend %Hpe_Layer_Style_01;
  height: 432px;
  width: 500px;
  position: fixed;
  z-index: 1040;
  left: 0;
  top: 15%;
  right: 0;
  margin-left: auto;
  margin-right: auto;

  .modal-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 7px 20px 7px 20px;
    border-bottom: 1px solid $HpeColorGrey_10;

    h3 {
      @extend %Hpe_Font_Style_03_05;
    }

    .close-modal {
      height: 24px;
      width: 24px;
      display: flex;
      cursor: pointer;
    }
  }

  .modal-content {
    height: 90%;
    padding-top: 12px;

    .server-connection-app {
      height: 100%;

      .server-connection-list {
        padding: 0 20px 20px 20px;
        height: 90%;
        overflow: auto;
        border-bottom: 1px solid $HpeColorGrey_10;

        .server-connection-item {
          width: 458px;
          height: 76px;
          display: flex;
          align-items: center;
          padding: 8px 12px 8px 12px;
          border-bottom: 1px solid $HpeColorGrey_10;

          &:hover {
            background-color: $HpeColorGrey_15;

            .hosts-buttons {
              button {
                display: flex;
              }
            }
          }

          .hosts-info {
            display: flex;
            flex-direction: column;
            align-self: flex-start;

            .host {
              display: flex;
              flex-direction: row;
              align-items: flex-start;
              justify-content: space-between;
            }

            .status {
              display: flex;

              i {
                width: 16px;
                height: 16px;
                margin-right: 4px;
              }

              span {
                @extend %Hpe_Font_Style_02_12;

                &.error {
                  @extend %Hpe_Font_Style_02_13;
                }
              }
            }

            .host-info-group:first-child {
              margin-right: 24px;
            }

            .host-info-group {
              margin-bottom: 12px;
              width: 114px;

              h3 {
                @extend %Hpe_Font_Style_01_12;
                max-width: 264px;
                text-transform: uppercase;
                margin-bottom: 4px;
              }

              h4 {
                @extend %Hpe_Font_Style_03_05;
                max-width: 264px;
                display: flex;

                i {
                  height: 16px;
                }
              }
            }
          }

          .hosts-buttons {
            display: flex;
            flex: 1;
            height: 100%;
            align-items: center;
            justify-content: flex-end;

            button {
              display: none;
              height: 20px;
              width: 20px;
              background-color: transparent;
              border: 0;
              margin-left: 12px;
            }
          }
        }

        .server-connection-add-item {
          width: 458px;
          height: 76px;
          display: flex;
          align-items: center;
          justify-content: space-between;
          padding: 14px 12px 8px 12px;
          border-top: 1px solid $HpeColorGrey_10;

          form {
            display: flex;
            align-items: center;
            flex: 1;
            margin-top: -19px;
            &.ng-submitted {
              margin-top: -3px;

              .hosts-info {
                margin-top: -14px;
              }
            }        


            .hosts-info {
              display: flex;
              flex-direction: row;
              align-items: flex-start;
              justify-content: space-between;

              .host-info-group:first-child {
                margin-right: 24px;
              }

              .host-info-group {
                margin-bottom: 12px;
                position: relative;

                h3 {
                  @extend %Hpe_Font_Style_01_12;
                  max-width: 264px;
                  text-transform: uppercase;
                  margin-bottom: 4px;
                }

                input {
                  padding: 2px;
                  max-width: 114px;
                  @extend %Hpe_Font_Style_02_07;

                  &.not-valid {
                    border: 1px solid red !important;
                  }
                }

                h4 {
                  @extend %Hpe_Font_Style_03_05;
                  max-width: 264px;
                  display: flex;

                  i {
                    height: 16px;
                  }
                }

                i {
                  width: 20px;
                  height: 20px;
                  position: absolute;
                  top: 15px;
                  right: 93px;
                }

                span {
                  position: absolute;
                  top: 22px;
                  right: -94px;
                  @extend %Hpe_Font_Style_02_13;
                
                  &.error-message {
                    position: relative;
                    top: 0px;
                    right: -2px;                
                  }
                }
              }
            }

            .hosts-buttons {
              display: flex;
              flex: 1;
              height: 100%;
              align-items: center;
              justify-content: flex-end;

              input {
                height: 24px;
                width: 24px;
                background-color: transparent;
                border: 0;
                margin-left: 12px;
              }
            }
            
              &.ng-submitted {
                .hosts-buttons {
                  margin-top: -15px;
                }
              }           
          }
        }
      }

      .server-connection-footer {
        display: block;
        align-items: center;
        padding: 10px 20px;
        height: 46px;

        button {
          background-color: transparent;
          border: none;
          padding: 0;

          span {
            @extend %Hpe_Font_Style_03_04;
          }
        }
      }
    }
  }
}
