/* specific styles definition for Job Cards */

.job-card-component {
  &:not(.online-card) {
    tooltip-component {
      .tooltip-container:hover {
        .tooltip-list {
          opacity: 0;
        }
      }
    }
  }
  // TODO: check deviceStatus because we are getting ready as status
  .card-progress-bar {
    &.ready {
      position: relative;
      height: 1px;
      background-color: #D5D5D5;
      display: flex;
      overflow: hidden;
      .fixed-progress {
        #ngProgress {
          .inner-progress {
            background-color: #D5D5D5;
          }
        }
      }
    }
  }

  &.small {
    .card-progress-bar {
      &.printing {
        height: 3px;
        .fixed-progress {
          width:452px;
          #ngProgress {
            height: 3px;
          }
        }
      }

      &.printing, &.ready {
        .WarmingUp {
          width: 10%;
        }

        .Printing {
          width: 90%;
        }

        .Cooling {
          width: 100%;
        }

        :first-child {
          position: absolute;
          height: 100%;


        }

        :last-child {
          height: 100%;
          width: 100%;
          display: flex;
          z-index: 1;

          div {
            position: relative;
          }

          .warming-up {
            width: 10%;
            border-right: 4px solid white;
          }

          .printing {
            width: 80%;
          }

          .aniling {
            width: 10%;
            border-left: 4px solid white;
          }
        }

      }
    }

    .card-component-content {
      display: flex;
      flex-direction: row;
      margin-top: 12px;
      flex: 1;

      &.is-empty {
        .status-content {
          display: flex;
          flex-direction: row;
          flex: 1;

          .unit {
            width: 96px;
          }

          .info {
            display: flex;
            justify-content: space-between;
          }
        }
      }

      &.is-ready {
        .status-content {
          display: flex;
          flex-direction: row;
          flex: 1;

          .unit {
            width: 96px;
          }

          .info {
            display: flex;
            justify-content: space-between;
          }
        }
      }

      &.is-printing,
      &.is-printed {

        .status-content {
          display: flex;
          flex-direction: row;
          flex: 1;

          .unit {
            width: 96px;
          }

          .info {
            display: flex;
            justify-content: space-between;
          }
        }
      }
    }
  }

  &.large {
    .critical-error-layer {
      height:474px;
    }

    .card-progress-bar {
      position: relative;
      height: 1px;
      background-color: #D5D5D5;
      display: flex;
      overflow: hidden;

      &.printing {
        height: 4px;

        .WarmingUp {
          width: 10%;
        }

        .Printing {
          width: 90%;
        }

        .Cooling {
          width: 100%;
        }

        :first-child {
          position: absolute;
          height: 100%;

          .fixed-progress {
            width:844px;
            #ngProgress {
              height: 4px;
            }
          }
        }

        :last-child {
          height: 100%;
          width: 100%;
          display: flex;
          z-index: 1;

          div {
            position: relative;
          }

          .warming-up {
            width: 10%;
            border-right: 4px solid white;
          }

          .printing {
            width: 80%;
          }

          .aniling {
            width: 10%;
            border-left: 4px solid white;
          }
        }
      }
    }

    .card-component-content {
      &.is-empty {
        .status-content {
          display: flex;
          flex-direction: row;
          flex: 1;

          .unit {
            width: 120px;
            margin-right: 44px;
          }

          .info {
            display: flex;
            justify-content: space-between;
            flex: 1;
          }
        }
      }

      &.is-ready {
        .status-content {
          display: flex;
          flex-direction: row;
          flex: 1;

          .unit {
            width: 120px;
            margin-right: 44px;
          }

          .info {
            display: flex;
            justify-content: space-between;
            flex: 1;
          }
        }
      }

      &.is-printing {
        .status-content {
          display: flex;
          flex-direction: row;
          flex: 1;

          .unit {
            width: 120px;
            margin-right: 44px;
          }

          .info {
            display: flex;
            justify-content: space-between;
            flex: 1;
          }
        }
      }

      &.is-printed {
        .status-content {
          display: flex;
          flex-direction: row;
          flex: 1;

          .unit {
            width: 120px;
            margin-right: 44px;
          }

          .info {
            display: flex;
            justify-content: space-between;
            flex: 1;
          }
        }
      }

    }
  }

}
