.widget-card-component {
  width: 184px;
  height: 204px;
  padding: 16px 12px 12px 12px;

  display: flex;
  flex-direction: column;
  position: relative;
  background: white;
  border-radius: 2px;

  &.online-card {
    box-shadow: 0px 2px 3px rgba(12, 12, 12, 0.2);
  }
  &:not(.online-card) {
    tooltip-component {
      .tooltip-container:hover {
        .tooltip-list {
          opacity: 0;
        }
      }
    }
  }
  .offline-card-layer {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: $HpeColorGrey_15;
    z-index: 3;
    opacity: 0.6;
  }

  .critical-error {
    position: absolute;
    z-index: 2;
    left: 0;
    right: 0;
  }

  .critical-error-layer {
    width: 100%;
    height: 146px;
    bottom: 0;
    background: $HpeColorWhite_01;
    opacity: 0.9;
  }

  .critical-error-icon {
    width: 72px;
    height: 72px;
    bottom: 0;
    margin: 0 auto;
    top: 54px;
    margin-top: 12px;
  }

  .critical-error-text {
    margin: 0 auto;
    bottom: 21px;
    padding: 0 20px 0 20px;
    text-align: center;

    p {
      @extend %Hpe_Font_Style_02_05;
      @extend %Paragraph_Style_20;
      text-transform: none;
      margin-top: 4px;
    }
  }

  .card-status-bar {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;

    .card-status-bar-container {
      width: 100%;
      left: 0;
      height: 4px;
      top: 0;
      border-radius: 0;
      margin: 0;

      &.error {
        background-color: $HpeColorRed_02;
      }

      &.warning {
        background-color: $HpeColorYellow_02;
      }
    }
  }

  .card-component-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-bottom: 8px;

    .card-component-header-printer {
      width: 45px;
      height: 30px;
      position: relative;

      .icon-status {
        right: -10px;
        top: -9px;
        position: absolute;
        width: 20px;
        height: 20px;
      }
    }

    .card-component-header-info {
      max-width: 104px;
      flex-basis: 104px;
      display: flex;
      flex-direction: column;
      height: 34px;
      justify-content: space-between;

      h1,
      h2 {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        width: 100%;
      }

      h1 {
        @extend %Hpe_Font_Style_03_05;
        @extend %Paragraph_Style_13;
        margin-top: -1px;
        text-transform: none;
      }

      h2 {
        @extend %Hpe_Font_Style_02_12;
        @extend %Paragraph_Style_25;
      }
    }

    .card-component-header-date {
      text-align: right;
      text-transform: capitalize;
      max-width: 104px;
      flex-basis: 104px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      height: 34px;

      h3 {
        @extend %Hpe_Font_Style_03_05;
      }
    }
  }

  .card-progress-bar {
    position: relative;
    height: 1px;
    background-color: #D5D5D5;
    display: flex;
    overflow: hidden;


    &.printing {
      height: 2px;

      .WarmingUp {
        width: 10%;
      }

      .Printing {
        width: 90%;
      }

      .Cooling {
        width: 100%;
      }

      :first-child {
        position: absolute;
        height: 100%;

        .fixed-progress {
          width:160px;
          #ngProgress {
            height: 2px;
          }
        }
      }

      :last-child {
        height: 100%;
        width: 100%;
        display: flex;
        z-index: 1;

        div {
          position: relative;
        }

        .warming-up {
          width: 10%;
          border-right: 4px solid white;
        }

        .printing {
          width: 80%;
        }

        .aniling {
          width: 10%;
          border-left: 4px solid white;
        }
      }
    }
  }

  .card-component-content {
    display: flex;
    flex-direction: row;
    margin-top: 12px;

    &.is-empty {
      .status-content {
        display: flex;
        height: 120px;

        .unit {
          width: 86px;
        }

        .info {
          width: 72px;
        }
      }
    }

    &.is-ready {
      .status-content {
        display: flex;
        height: 120px;

        .unit {
          width: 86px;
        }

        .info {
          width: 72px;
        }
      }
    }

    &.is-printing {
      .status-content {
        display: flex;
        height: 120px;
        width: 160px;

        .view {
          width: 100%;
          height: 100%;
          position: relative;
        }
      }
    }

    &.is-printed {
      .status-content {
        display: flex;
        height: 120px;
        width: 160px;

        .view {
          width: 100%;
          height: 100%;
          position: relative;
        }
      }
    }
  }

}
