.agents-component {
  position: relative;
  display: flex;
  justify-content: center;

  .small {
    height: 100%;
    max-width: 72px;

    h3 {
      @extend %Hpe_Font_Style_01_12;
      text-align: center;
      text-transform: uppercase;
      height: 26px;

      &.no-height {
        height: auto;
        margin-bottom: 4px;
      }
    }

    .agents-container {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      width: 48px;
      height: 48px;

      .agent-placeholder {
        width: 16px;
        height: 36px;
        margin: 2px;
        position: relative;

        .icon-advice {
          position: absolute;
          height: 20px;
          width: 20px;
          right: 0;
          left: -2px;
          top: 0;
          bottom: 0;
          margin-left: auto;
          margin-right: auto;
          margin-top: auto;
          margin-bottom: auto;
        }
      }
    }
  }

  .large {
    width: 100%;
    height: 172px;

    h3 {
      @extend %Hpe_Font_Style_01_12;
      text-align: center;
      text-transform: uppercase;
      margin-bottom: 16px;
    }

    .agents-container {
      display: flex;
      flex-direction: row;
      justify-content: center;
      width: 100%;
      height: 144px;

      .agent-container {
        margin-left: 12px;
        margin-right: 12px;

        .agent-placeholder {
          margin: auto;
          width: 30px;
          height: 108px;
          position: relative;

          .icon-advice {
            position: absolute;
            height: 20px;
            width: 20px;
            right: 0;
            left: 0;
            top: 0;
            bottom: 0;
            margin-left: auto;
            margin-right: auto;
            margin-top: auto;
            margin-bottom: auto;
          }
        }

        .agent-type,
        .agent-level {
          text-align: center;

          h4 {
            @extend %Hpe_Font_Style_02_01;
            text-transform: capitalize;
          }

          h5 {
            @extend %Hpe_Font_Style_02_01;
            text-transform: capitalize;
          }
        }

        .agent-type {
          margin-bottom: 8px;
        }

        .agent-level {
          margin-top: 6px;
        }
      }
    }
  }
}
