.processing-component {
  height: 100%;

  .small {
    width: 96px;
    height: 100%;

    h3 {
      @extend %Hpe_Font_Style_01_12;
      text-transform: uppercase;
      margin-bottom: 8px;
    }

    .job-status {
      display: flex;
      align-items: center;
      height: 32px;
      margin-bottom: 4px;
      padding-left: 12px;
      background-color: $HpeColorBlue_07;

      h5 {
        @extend %Hpe_Font_Style_03_05;
      }
    }

    .job-placeholder {
      height: 72px;
      width: 96px;
      margin-bottom: 12px;
    }

    .unit-info-status {
      display: flex;
      flex-direction: column;
      margin-left: 12px;

      div:first-child {
        margin-bottom: 8px;
        display: flex;

        i {
          width: 12px;
          height: 12px;
        }

        h5 {
          @extend %Hpe_Font_Style_02_05;
          margin-left: 4px;

        }
      }

      div:last-child {
        display: flex;

        h5:first-child {
          margin-right: 12px;
          width: 100%;
          text-overflow: ellipsis;
          white-space: nowrap;
          overflow: hidden;
        }

        h5 {
          @extend %Hpe_Font_Style_02_05;
        }
      }
    }
  }

  .large {
    width: 120px;
    height: 100%;

    h3 {
      @extend %Hpe_Font_Style_01_12;
      text-transform: uppercase;
      margin-bottom: 16px;
    }

    .job-status {
      display: flex;
      align-items: center;
      height: 32px;
      margin-bottom: 4px;
      padding-left: 12px;
      background-color: $HpeColorBlue_07;

      h5 {
        @extend %Hpe_Font_Style_03_05;
      }
    }

    .job-placeholder {
      height: 90px;
      width: 120px;
      margin-bottom: 12px;
    }

    h4 {
      @extend %Hpe_Font_Style_03_05;
      margin-bottom: 12px;
    }

    .unit-info-status {
      display: flex;
      flex-direction: column;

      div {
        margin-bottom: 12px;

        h3 {
          margin-bottom: 4px;
        }

        span {
          @extend %Hpe_Font_Style_03_05;
          margin-bottom: 20px;
        }

        i {
          width: 16px;
          height: 16px;
        }

        h5 {
          @extend %Hpe_Font_Style_03_05;
          margin-left: 4px;
          display: inline-block;
          word-break: break-all;
          width: 100%;
        }
      }

      .status {
        display: flex;
        align-items: center;
      }
    }
  }

}
