.splash-component {
  @extend %Hpe_Layer_Style_08;
    width: 636px;
    height: 384px;
    margin-left: auto;
    margin-right: auto;
    padding: 24px 26px 48px 26px;

  h3 {
    @extend %Hpe_Font_Style_02_06;
    @extend %Paragraph_Style_06;
    display: flex;
    justify-content: flex-end;
    height: 40px;
    align-items: center;
        text-transform: capitalize;
  }

  .splash-info {
    @extend %Hpe_Layer_Style_09;
    height: 100%;
    width: 100%;
    padding: 117px 76px 113px 46px;
    display: flex;
    flex-direction: row;

    .logo {
      display: flex;
      flex-direction: row;
      align-items: center;
      padding-right: 12px;
      margin-right: 12px;
      border-right: 2px solid $HpeColorBlue_03;

      i {
        width: 60px;
        height: 60px;
        margin-right: 12px;
      }
    }

    .software {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: center;
      margin-left: 12px;

      h1 {
        @extend %Hpe_Font_Style_Splash_Screen;
        @extend %Paragraph_Style_01;
        text-transform: capitalize;
        display: inline-block;
        margin-top: -2px;
        margin-bottom: 13px;
      }

      h2 {
        @extend %Hpe_Font_Style_03_01;
        @extend %Paragraph_Style_01;
      }
    }
  }
}
