.header-component {
  background: $HpeColorBlue_03;
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 48px;
  position: absolute;
  z-index: 1;
  width: 100%;
  padding-top:5px;

  .logo {
    height: 100%;
    width: 30px;
    margin: 0 12px;
    -webkit-app-region: drag;
    margin-top:-5px;
  }

  .title {
    flex: 1;
    height: 100%;
    display: flex;
    align-items: center;
    -webkit-app-region: drag;
    margin-top:-5px;
    @media screen and (max-width: 541px) {
      display: none;
    }

    h1 {
      @extend %Hpe_Font_Style_App_Header;
      letter-spacing: 0.18pt;
    }
  }

  .window {
    height: 100%;
    text-align: right;
    -webkit-app-region: drag;
    margin-top:-5px;
    @media screen and (max-width: 541px) {
      flex: 1;
    }

    .window-btn {
      width: 24px;
      height: 24px;
      display: inline-block;
      cursor: pointer;
      background: transparent;
      border: none;
      padding: 0;
      -webkit-app-region: no-drag;

      &#minimize-window-button {
        margin-right: 1px;
      }

      &#maximize-window-button {
        @media screen and (max-width: 541px) {
          display: none;
        }
      }

      &#close-window-button {
        margin-left: 1px;
      }
    }
  }
}
