.download-about-modal {
  @extend %Hpe_Layer_Style_01;
  height: 344px;
  width: 420px;
  position: fixed;
  z-index: 1040;
  left: 0;
  top: 15%;
  right: 0;
  margin-left: auto;
  margin-right: auto;

  .modal-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 7px 20px 7px 20px;
    border-bottom: 1px solid $HpeColorGrey_10;

    h3 {
      @extend %Hpe_Font_Style_03_05;
    }

    .close-modal {
      height: 24px;
      width: 24px;
      display: flex;
      cursor: pointer;
    }
  }

  .download-about-component {
    height: 304px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .download-about-content {
      padding: 20px 20px 0 20px;
      overflow: auto;

      h1 {
        @extend %Hpe_Font_Style_14_05;
        margin-bottom: 4px;
      }

      h3 {
        @extend %Hpe_Font_Style_03_05;

        &.printer-name {
          margin-top: 24px;
          margin-bottom: 24px;
        }
      }

      .printer-data {
        width: 70%;
        margin-bottom: 24px;

        .printer-data-section {
          display: flex;
          justify-content: space-between;

          h3 {
            @extend %Hpe_Font_Style_03_05;
            margin-bottom: 4px;
            margin-right: 24px;
            width: 204px;
          }

          span {
            @extend %Hpe_Font_Style_03_07;
            width: 150px;
          }
        }
      }

      .printer-download-status {
        overflow: hidden;
        .animated-progress {
          @include animated-progress;
          height: 4px;
        }
        .download-message {
          margin-top: 8px;
          @extend %Hpe_Font_Style_03_05;
        }
        .ready-message {
          @extend %Hpe_Font_Style_03_05;
          span {
            margin-left: 18px;
          }
        }
        .icon-status-ready {
          display: inline-block;
          height: 18px;
          width: 18px;
        }
      }
    }

    .download-about-footer {
      display: flex;
      flex-direction: row;
      justify-content: flex-end;
      align-items: center;
      padding: 7px 20px 7px 20px;
      border-top: 1px solid $HpeColorGrey_10;

      a,
      button {
        width: 47%;
        text-align: center;
        cursor: pointer;
        &:disabled {
          color: #D5D5D5 !important;
        }
      }

      .cancel-button {
        @extend %Button_style_02;
        + .ok-button {
          margin-left: 10px;
        }
      }

      .ok-button {
        @extend %Button_style_01;
        // text-transform: uppercase;
      }

      .open-button {
        width: 150px !important;
      }
    }
  }
}
