.cleaning-rolls-component {
  position: relative;
  display: flex;
  justify-content: center;

  .small {
    height: 100%;
    max-width: 72px;

    h3 {
      @extend %Hpe_Font_Style_01_12;
      text-align: center;
      text-transform: uppercase;
      height: 26px;
    }

    .cleaning-roll-placeholder {
      width: 48px;
      height: 48px;
      margin: auto;
      position: relative;

      .icon-advice {
        height: 20px;
        width: 20px;
        position: absolute;
        right: 0;
        top: 0;
      }

    }
  }

  .large {
    width: 100%;
    height: 172px;

    h3 {
      @extend %Hpe_Font_Style_01_12;
      text-align: center;
      text-transform: uppercase;
      margin-bottom: 16px;
    }

    .cleaning-rolls-container {
      display: flex;
      flex-direction: column;
      justify-content: center;
      width: 100%;
      height: 144px;

      .cleaning-roll-placeholder {
        margin: auto;
        height: 120px;
        width: 120px;
      }

      .icon-advice {
        position: absolute;
        height: 24px;
        width: 24px;
        right: 0;
        left: 0;
        top: 87px;
        bottom: 0;
        margin-left: auto;
        margin-right: auto;
        margin-top: auto;
        margin-bottom: auto;
      }
      .cleaning-roll-consumed {
        margin-top: 5px;
        text-align: center;

        h4 {
          @extend %Hpe_Font_Style_02_01;
        }
      }
    }
  }
}
