.heating-lamps-component {
  position: relative;
  display: flex;
  justify-content: center;

  .small {
    height: 100%;
    max-width: 72px;

    h3 {
      @extend %Hpe_Font_Style_01_12;
      text-align: center;
      text-transform: uppercase;
      height: 26px;
    }

    .heating-lamp-placeholder {
      width: 48px;
      height: 48px;
      margin: auto;
      position: relative;

      .icon-advice {
        height: 20px;
        width: 20px;
        position: absolute;
        right: 0;
        top: 0;
      }

    }
  }

  .large {
    width: 100%;
    height: 172px;

    h3 {
      @extend %Hpe_Font_Style_01_12;
      text-align: center;
      text-transform: uppercase;
      margin-bottom: 16px;
    }

    .heating-lamps-container {

      display: block;
      width: 204px;
      height: 144px;

      .heating-lamps-row {

        display:block;
        width: 180px;
        height:144px;
        margin: 0 17%;

        .west {
          //transform: rotate(270deg);
        }
        .north {
          transform: rotate(450deg);
          tooltip-component {
            transform: rotate(270deg);
          }
        }
        .south {
          transform:rotate(270deg);
          tooltip-component {
            transform: rotate(90deg);
          }
        }

        .east {
          transform:rotate(180deg);
          tooltip-component {
            transform: rotate(180deg);
          }
        }

        .heating-lamps-col {
          display: inline-block;
          //width:17%;
          margin-bottom: 6px;

          .heating-lamp-placeholder {
            width: 26px;
            height: 12px;
            position: relative;
            display: inline-block;
            &:hover {
              z-index: 9;
              .tooltip-list {
                pointer-events: none;
              }
            }
            .icon-advice {
              position: absolute;
              height: 20px;
              width: 20px;
              right: 0;
              left: 0;
              top: 0;
              bottom: 0;
              margin-left: auto;
              margin-right: auto;
              margin-top: auto;
              margin-bottom: auto;
            }

            &.icon {
              display: block;
            }

            &:nth-child(1) {
              margin-right:5px;
            }
            &:nth-child(3), &:nth-child(4) {
              margin-left:-2px;
            }

            &:nth-child(5) {
              margin-left:5px;
            }
          }
        }

        .heating-lamps-col:nth-child(1) {
            margin-bottom: -5px;
          .north, .south {
            margin-top:-7px;
          }
        }
        .heating-lamps-col:nth-child(3), .heating-lamps-col:nth-child(4), .heating-lamps-col:nth-child(6) {
          margin-bottom:0px;
        }
        .heating-lamps-col:nth-child(7) {
            margin-top: -6px;
          .north, .south {
            margin-top:7px;
          }
        }

      }
    }
  }
}
