%Paragraph_Style_01 {
  text-transform: capitalize;
  vertical-align: middle;
  text-align: left;
  white-space: nowrap;
  -webkit-line-clamp: 1000;
  -webkit-box-orient: vertical;
  text-overflow: clip ellipsis;
}

