.fusing-lamps-component {
  position: relative;
  display: flex;
  justify-content: center;

  .small {
    height: 100%;
    max-width: 72px;

    h3 {
      @extend %Hpe_Font_Style_01_12;
      text-align: center;
      text-transform: uppercase;
      height: 26px;
    }

    .fusing-lamp-placeholder {
      width: 48px;
      height: 48px;
      margin: auto;
      position: relative;

      .icon-advice {
        height: 20px;
        width: 20px;
        position: absolute;
        right: 0;
        top: 0;
      }

    }
  }

  .large {
    width: 100%;
    height: 172px;

    h3 {
      @extend %Hpe_Font_Style_01_12;
      text-align: center;
      text-transform: uppercase;
      margin-bottom: 16px;
    }

    .fusing-lamps-container {
      display: flex;
      justify-content: space-around;
      width: 130px;
      height: 144px;
      margin: 0 auto;

      .fusing-lamps-row {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        width: 50px;

        .fusing-lamp-number {
          @extend %Hpe_Font_Style_02_01;
          margin-bottom: 2px;
          text-align: center;
        }

        .fusing-lamp-placeholder {
          width: 20px;
          height: 122px;
          position: relative;

          .icon-advice {
            position: absolute;
            height: 20px;
            width: 20px;
            right: 0;
            left: 0;
            top: 0;
            bottom: 0;
            margin-left: auto;
            margin-right: auto;
            margin-top: auto;
            margin-bottom: auto;
          }
        }
      }
    }
  }
}
