@charset "UTF-8";

$imgs_folder: '../images';

$min-width: em(530);
$max-width: em(870);

// Media queries breakpoints
$min-window:                530px;
$default-window:            870px;

$app-height:                      600px;
$app-header-height:               50px;
$app-content-topbar-height:       40px;
$app-content-topbar-inner-height: 80px;

$logo_size:                 30px;
$menu-left-width:           160px;

$pad1:                      10px;


// printer cards
$card-width: 492px;
$cards-margin:              9px;
$printer-card-width:        330px;
$printer-card-height:       230px;
$printer-card-pad:          20px;
$printer-card-opacity:      .75;


// Typography
$f-hp:            'hp', arial, sans-serif;
$f-hp_light:      'hp_light', arial, sans-serif;
$f-hp_bold:       'hp_bold', arial, sans-serif;
$f-system:        Arial, sans-serif;
$f-systemMT:      ArialMT, 'Arial MT', sans-serif;
$f-systemNarrow:  ArialNarrow, 'Arial Narrow', sans-serif;

$f-base: 16;

// Colors
// blue
$c-01:  #0096D6;
$c-02:  #0F7BBB;
$c-03:  #006699;
// blue lighten
$c-13:  #c1e5ee;
$c-14:  #adc9d0;

// red
$c-04:  #F05332;
$c-05:  #d5421d;
$c-06:  #b33817;

// green
$c-07:  #EECC10;
$c-08:  #53A754;
$c-09:  #04701E;

// yellow
$c-10:  #04701E;
$c-11:  #EECC10;
$c-12:  #F4B51E;

// yellow
$c-10:  #f1ce7e;
$c-11:  #edcb31;
$c-12:  #f2b435;

// purple
$c-15:  #812d7f;

// grey
$c-16:  #0C0C0C;
$c-17:  #3e3e3e;
$c-18:  #535353;
$c-19:  #636363;
$c-20:  #a2a2a2;
$c-21:  #bfbfbf;
$c-22:  #d5d5d5;
$c-23:  #eee;
$c-24:  #fff;
$c-25:  #898989;
$c-26:  #546266;
$c-27:  #697c83;
$c-28:  #8598a2;
$c-29:  #1c1c1c;
$c-30:  #717171;

$c-31:  #d00202; // red

$c-background:  $c-23;
$c-horiz-line:  $c-22;


$radius-default:    2px;
$c-border:          $c-21;
$c-border-active-color:   $c-01;
$c-border-active:   2px solid $c-border-active-color;
$boxshadow:         0 2px 2px #cdcdcd;


// Menu (left)
$c-menu-color-link:           $c-18;
$c-menu-color-hover:          $c-menu-color-link;
$c-menu-color-selected:       $c-24;
$c-menu-background:           $c-22;
$c-menu-background-selected:  $c-20;
$c-menu-background-hover:     $c-21;


// alert colors
/* warning */
$alert-warning-color: $c-06;
$alert-warning-background: $c-10;
/* ready */
$alert-ready-color: $c-01;
$alert-ready-background: $c-13;
/* success */
$alert-success-color: $c-08;
$alert-success-background: lighten($c-08, 36%);
/* error */
$alert-error-color: $c-06;
$alert-error-background: lighten($c-06, 46%);
/* inactive */
$alert-inactive-color: $c-19;
$alert-inactive-background: $c-23;
