@import "./delete-printer/index";

.printer-management-modal {
  @extend %Hpe_Layer_Style_01;
  height: 432px;
  width: 500px;
  position: fixed;
  z-index: 1040;
  left: 0;
  top: 15%;
  right: 0;
  margin-left: auto;
  margin-right: auto;

  .modal-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 7px 20px 7px 20px;
    border-bottom: 1px solid $HpeColorGrey_10;

    h3 {
      @extend %Hpe_Font_Style_03_05;
    }

    .close-modal {
      height: 24px;
      width: 24px;
      display: flex;
      cursor: pointer;
    }
  }

  .modal-content {
    height: 90%;

    .printer-management-component {
      height: 100%;

      .printer-management-list {
        padding: 12px 20px 20px 20px;
        height: 350px;
        overflow: auto;
        border-bottom: 1px solid $HpeColorGrey_10;

        .printer-management-item {
          width: 460px;
          height: 140px;
          display: flex;
          align-items: center;
          padding: 8px 12px 8px 12px;
          border-bottom: 1px solid $HpeColorGrey_10;

          &:hover {
            background-color: $HpeColorGrey_15;

            .printer-buttons {
              button {
                display: flex;
              }
            }
          }

          &.printer-offline {
            .printer-icon {
              opacity: 0.4;
              .icon-status {
                display: none;
              }
            }
          }
          .printer-icon {
            width: 43px;
            height: 30px;
            margin-right: 20px;
            display: flex;
            position: relative;

            .icon-status {
              width: 20px;
              height: 20px;
              position: absolute;
              left: 33px;
              bottom: 18px;
            }
          }

          .printer-info {
            display: flex;
            flex-direction: column;
            align-self: flex-start;

            .printer-info-group {
              margin-bottom: 12px;
              overflow: hidden;
              width: 264px;

              h3 {
                @extend %Hpe_Font_Style_01_12;
                max-width: 264px;
                text-transform: uppercase;
                margin-bottom: 4px;
              }

              h4 {
                @extend %Hpe_Font_Style_03_05;
                max-width: 264px;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
                align-items: center;

                span {
                  margin-right: 4px;
                }

                i {
                  height: 20px;
                  width: 20px;
                  margin-right: 4px;
                }
              }
            }
          }

          .printer-buttons {
            display: flex;
            flex: 1;
            height: 100%;
            align-items: center;
            justify-content: flex-end;

            button {
              display: none;
              height: 24px;
              width: 24px;
              background-color: transparent;
              border: 0;
              margin-left: 12px;
            }
          }

          .form-to-edit {
              margin-top:16px;

            .printer-info {
                
                .with-message {
                  overflow: visible;
                  display: flex;

                  .error-message {
                    position: absolute;
                    top: 20px;
                  }
                }
             }
          }          
        }

        .printer-management-add-item, .printer-management-item {
          width: 460px;
          height: 140px;
          display: flex;
          align-items: center;
          justify-content: space-between;
          padding: 8px 12px 8px 12px;
          border-top: 1px solid $HpeColorGrey_10;

          .printer-icon {
            width: 43px;
            height: 30px;
            margin-right: 20px;
            display: flex;
            position: relative;
          }

          form {
            display: flex;
            align-items: center;
            flex: 1;

            input {
              @extend %Hpe_Font_Style_02_07;
              padding: 3px 8px;
              
              &.not-filled {
                  border: 1px solid red;
              }
            }

            .printer-info {
              display: flex;
              flex-direction: column;
              align-self: flex-start;

              .printer-info-group {
                margin-bottom: 12px;
                position: relative;

                &.first-group-with-top-err {
                   margin-bottom: -3px;
                }
                 &.first-group-with-bottom-err {
                   margin-top: 17px;
                }

                input {
                  @extend %Hpe_Font_Style_02_07;
                  padding: 3px 8px;
                }                

                h3 {
                  @extend %Hpe_Font_Style_01_12;
                  max-width: 264px;
                  text-transform: uppercase;
                  margin-bottom: 4px;
                }

                h4 {
                  @extend %Hpe_Font_Style_03_05;
                  max-width: 264px;
                  display: flex;

                  i {
                    height: 16px;
                  }
                }

                i {
                  width: 20px;
                  height: 20px;
                  position: absolute;
                  top: 17px;
                  right: 0;
                }

                span {
                  position: absolute;
                  top: 22px;
                  right: -94px;
                  @extend %Hpe_Font_Style_02_13;
                }

                span.error-message {
                  position: relative;
                  top: -20px;
                  left: 173px;
                  @extend %Hpe_Font_Style_02_13;
                }
              }
            }

            .printer-buttons {
              display: flex;
              flex: 1;
              height: 100%;
              align-items: center;
              justify-content: flex-end;

              input {
                height: 24px;
                width: 24px;
                background-color: transparent;
                border: 0;

                &.icon-printer-management-confirm{
                  margin-left: 12px;
                }
              }
            }

          }
        }
      }

      .printer-management-footer {
        display: block;
        align-items: center;
        padding: 10px 20px;
        height: 46px;

        button {
          background-color: transparent;
          border: none;
          padding: 0;

          span {
            @extend %Hpe_Font_Style_03_04;
          }
        }
      }
    }
  }
}
