.build-unit-component {
  position: relative;

  &.widget {
    .empty {
      h3 {
        @extend %Hpe_Font_Style_01_12;
        text-transform: uppercase;
        max-width: 86px;
        margin-bottom: 4px;
      }

      .build-unit-placeholder {
        max-width: 86px;
        margin-bottom: 20px;
        height: 108px;

        h4 {
          @extend %Hpe_Font_Style_05_05;
        }
      }
    }

    .ready {
      .build-unit-info {
        max-width: 100%;
        margin-bottom: 20px;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;

        h3 {
          @extend %Hpe_Font_Style_01_12;
          text-transform: uppercase;
          margin-bottom: 4px;
        }
        span {
          @extend %Hpe_Font_Style_05_05;
          text-transform: uppercase;

        }
      }
    }
  }

  &.small {
    .empty {
      h3 {
        @extend %Hpe_Font_Style_01_12;
        text-transform: uppercase;
        max-width: 96px;
        margin-bottom: 4px;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
      }

      .build-unit-placeholder {
        max-width: 96px;
        margin-bottom: 20px;
        height: 108px;
        text-overflow: ellipsis;
        overflow: hidden;

        h4 {
          @extend %Hpe_Font_Style_05_05;
        }
      }
    }

    .ready {
      .build-unit-info {
        max-width: 100%;
        margin-bottom: 20px;
        text-overflow: ellipsis;
        overflow: hidden;

        white-space: nowrap;

        &:first-child {
          max-height: 53px;
        }

        h3 {
          @extend %Hpe_Font_Style_01_12;
          text-transform: uppercase;
          margin-bottom: 4px;
        }

        span {
          @extend %Hpe_Font_Style_05_05;
          display: inline-block;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
          width: 100%;
          &:first-letter{
            text-transform: uppercase;

          }
        }
      }
    }

    .printing {
      .build-unit-info {
        max-width: 100%;
        margin-bottom: 20px;
        text-overflow: ellipsis;
        overflow: hidden;

        h3 {
          @extend %Hpe_Font_Style_01_12;
          text-transform: uppercase;
          margin-bottom: 4px;
        }

        span {
          @extend %Hpe_Font_Style_05_07;

          &.build-unit-progress {
            @extend %Hpe_Font_Style_05_11;
          }
        }

      }
    }

    .printed {
      width: 96px;
      line-height: 0.925;

      h3 {
        @extend %Hpe_Font_Style_01_12;
        text-transform: uppercase;
        margin-bottom: 8px;
      }

      .build-unit-info {
        @extend %Hpe_Font_Style_05_05;
        max-width: 100%;
        margin-bottom: 16px;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
        text-transform: capitalize;

        &:first-child {
          max-height: 53px;
        }

        .build-unit-progress {
          @extend %Hpe_Font_Style_05_04;
        }
      }
    }
  }

  &.large {
    .empty {
      h3 {
        @extend %Hpe_Font_Style_01_12;
        text-transform: uppercase;
        max-width: 96px;
        margin-bottom: 4px;
        overflow-wrap: break-word;
      }

      .build-unit-placeholder {
        max-width: 100%;
        margin-bottom: 20px;
        overflow-wrap: break-word;

        h4 {
          @extend %Hpe_Font_Style_05_05;
        }
      }
    }

    .ready {
      width: 114px;
      height: 168px;

      .build-unit-info {
        max-width: 100%;
        margin-bottom: 20px;
        overflow-wrap: break-word;

        h3 {
          @extend %Hpe_Font_Style_01_12;
          text-transform: uppercase;
          margin-bottom: 4px;
        }

        span {
          @extend %Hpe_Font_Style_05_05;
          display: inline-block;
          overflow-wrap: break-word;
          width: 100%;
          &:first-letter {
            text-transform: uppercase;
          }
        }
      }
    }

    .printing, .printed {
      margin: 0 auto;
      width: 114px;
      height: 168px;

      .build-unit-info {
        line-height: 1;
        max-width: 100%;
        margin-bottom: 20px;
        overflow-wrap: break-word;

        h3 {
          @extend %Hpe_Font_Style_01_12;
          text-transform: uppercase;
          margin-bottom: 4px;
        }

        span {
          @extend %Hpe_Font_Style_05_07;

          &.build-unit-progress {
            @extend %Hpe_Font_Style_05_11;
          }
        }
      }
    }

    .printed {
      height:182px;
      .build-unit-info {
        margin-bottom: 28px;
      }
    }
  }
}
