.printer-about-modal {
  @extend %Hpe_Layer_Style_01;
  height: 416px;
  width: 420px;
  position: fixed;
  z-index: 1040;
  left: 0;
  top: 15%;
  right: 0;
  margin-left: auto;
  margin-right: auto;

  .modal-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 7px 20px 7px 20px;
    border-bottom: 1px solid $HpeColorGrey_10;

    h3 {
      @extend %Hpe_Font_Style_03_05;
    }

    .close-modal {
      height: 24px;
      width: 24px;
      display: flex;
      cursor: pointer;
    }
  }

  .printer-about-component {
    height: 388px;

    .printer-about-content {
      height: 85%;
      padding: 20px 20px 0 20px;
      overflow: auto;

      h1 {
        @extend %Hpe_Font_Style_14_05;
        margin-bottom: 4px;
      }

      h3 {
        @extend %Hpe_Font_Style_03_05;
        margin-bottom: 24px;
      }

      .printer-data:last-child {
        margin-bottom: 24px;
      }

      .printer-data {
        margin-bottom: 12px;
        width: 70%;

        .printer-data-section {
          display: flex;
          justify-content: space-between;

          h3 {
            @extend %Hpe_Font_Style_03_05;
            margin-bottom: 4px;
            margin-right: 24px;
            width: 204px;
          }

          span {
            @extend %Hpe_Font_Style_03_07;
            width: 150px;
          }
        }
      }

      .printer-links {
        margin-bottom: 36px;
        display: flex;
        flex-direction: column;

        a {
          display: inline-table;
          @extend %Hpe_Font_Style_03_05;
          color: #0F7BBB;
          text-decoration: underline;
        }

        :first-child {
          margin-bottom: 4px;
        }
      }
    }

    .printer-about-footer {
      display: flex;
      flex-direction: row;
      justify-content: flex-end;
      align-items: center;
      padding: 7px 20px 7px 20px;
      border-top: 1px solid $HpeColorGrey_10;

      a {
        width: 47%;
        text-align: center;
        cursor: pointer;
      }

      .cancel-button {
        @extend %Button_style_02;
      }

      .ok-button {
        @extend %Button_style_01;
        // text-transform: uppercase;
      }
    }
  }
}
