tooltip-component {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  z-index: 1;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;

  .tooltip-container {
    width: 100%;
    height: 100%;

    .tooltip-display {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      box-sizing: border-box;
      height: 100%;
    }

    .tooltip-list {
      position: relative;
      opacity:0;
      display: none;
      transition: opacity ease-in-out 0.2s;
      position: absolute;
      top: 0;
      width: 100%;
      height: 100%;

      @media screen and (max-width: 202px) {
        position: fixed;
        height: 100%;
        width: 100%;
        left: -6px;
        transition: height 250ms;
      }

      .arrow {
      width: 0;
      height: 0;
      border-left: 7px solid transparent;
      border-right: 7px solid transparent;
      border-bottom: 7px solid #B9B8BB;
      position: absolute;
      top: 15px;
      left: 2px;
      }

      .arrow:before {
        bottom: 100%;
        border: solid transparent;
        content: " ";
        height: 0;
        width: 0;
        position: absolute;
        pointer-events: none;
        border-color: rgba(221, 221, 221, 0);
        border-bottom-color: white;
        border-width: 8px;
        margin-left: -8px;
        right:-8px;
        top: -7px;
        z-index: 9999;

      }


      .container {
        position: absolute;
        width: 144px;
        z-index: 2;
        cursor: default;
        background: white;
        display: block !important;
        word-wrap: break-word;
        top: 21px;
        left: -64px;
        padding: 8px 12px 8px 12px;
        z-index: 9997;
        @extend %Hpe_Layer_Style_01;

        @media screen and (max-width: 202px) {
          width: 188px;
        }

        > div {
          h2 {
            @extend %Hpe_Font_Style_02_05;
          }
        }
      }
    }

    &:hover  {
      .tooltip-list {
        opacity:1;
        display: flex;
        transition:none;
      }
    }
  }
}
