@mixin vendor ($property, $value...) {
  -webkit-#{$property}:$value;
  -moz-#{$property}:$value;
  -ms-#{$property}:$value;
  -o-#{$property}:$value;
}

// TODO: Just use it on _icons.scss, refactore it soon
@mixin background-icon-retina($file, $width, $height, $subfolder:'') {
  width: $width;
  height: $height;
  background-image: url($imgs_folder + '/icons/x1/' + unquote($subfolder + '/') + $file + '.png');
  background-image: url($imgs_folder + '/icons/svg/'+ unquote($subfolder + '/') + $file + '@1x.svg'),linear-gradient(transparent, transparent);
  -webkit-background-size: $width $height;
  -moz-background-size: $width $height;
  -o-background-size: $width $height;
  background-size: $width $height;

  @media only screen and (-webkit-min-device-pixel-ratio: 2),
  only screen and (-moz-min-device-pixel-ratio: 2),
  only screen and (-o-min-device-pixel-ratio: 2/1),
  only screen and (min-device-pixel-ratio: 2),
  only screen and (min-resolution: 192dpi),
  only screen and (min-resolution: 2dppx) {
    & {
      background-image: url($imgs_folder + '/icons/x2/' + unquote($subfolder + '/') + $file + '@2x.png');
      -webkit-background-size: $width $height;
      -moz-background-size: $width $height;
      -o-background-size: $width $height;
      background-size: $width $height;
    }
  }

  @media only screen and (-webkit-min-device-pixel-ratio: 3),
  only screen and (-moz-min-device-pixel-ratio: 3),
  only screen and (-o-min-device-pixel-ratio: 3/1),
  only screen and (min-device-pixel-ratio: 3),
  only screen and (min-resolution: 401dpi),
  only screen and (min-resolution: 3dppx) {
    & {
      background-image: url($imgs_folder + '/icons/x3/' + unquote($subfolder + '/') + $file + '@3x.png');
      -webkit-background-size: $width $height;
      -moz-background-size: $width $height;
      -o-background-size: $width $height;
      background-size: $width $height;
    }
  }
}

@mixin keyframes($animationName) {
  @-webkit-keyframes #{$animationName} {
    @content;
  }
  @-moz-keyframes #{$animationName} {
    @content;
  }
  @-o-keyframes #{$animationName} {
    @content;
  }
  @keyframes #{$animationName} {
    @content;
  }
}

@mixin animate($name, $duration, $iteration, $direction) {
  -webkit-animation-duration: $duration;
     -moz-animation-duration: $duration;
       -o-animation-duration: $duration;
          animation-duration: $duration;
  -webkit-animation-iteration-count: $iteration;
     -moz-animation-iteration-count: $iteration;
       -o-animation-iteration-count: $iteration;
          animation-iteration-count: $iteration;
  -webkit-animation-name: $name;
     -moz-animation-name: $name;
       -o-animation-name: $name;
          animation-name: $name;
  -webkit-animation-direction: $direction;
     -moz-animation-direction: $direction;
       -o-animation-direction: $direction;
          animation-direction: $direction;
}

@mixin animated-progress {
  @-webkit-keyframes progress-bar {
    0% { background-position: left bottom; }
    100% { background-position: right bottom; }
  }

  width: 200% !important;
  background: -webkit-linear-gradient(
       left,
       #D5D5D5 90%,
       white 100%
   ) repeat;
  -webkit-background-size: 50% 100%;
  -webkit-animation-name: progress-bar;
  -webkit-animation-duration: 2s;
  -webkit-animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
}
