.tabs-component {
  position: relative;
  height: 100%;
  flex: 1;
  &:after {
    content: '';
    display: block;
    width: 120px;
    height: 2px;
    background: transparent;
    position: absolute;
    bottom: 0;
    left: 24px;
    @media screen and (max-width: 541px){
      width: 108px;
      left: 8px;
    }
  }
  // calculate active position looping through the tabs
  @for $i from 0 through 1 {
    &.tab-selected-#{$i} {
      &:after {
        // enable transition when additional tabs are added
        // transition: left 1s;
        background: $HpeColorBlue_02;
        left: 120px * $i + 24;
        @media screen and (max-width: 541px){
          left: 108px * $i + 8;
        }
      }
    }
  }

  .nav-tabs {
    align-items: center;
    display: flex;
    flex-direction: row;
    height: 100%;
    border-bottom: 0;
    padding: 1px 0px 0 24px;

    @media screen and (max-width: 541px){
      padding: 1px 0 0 8px;

      // li:last-child {
      //   display: none;
      // }
    }

    li {
      cursor: pointer;
      float: none;
      height: 100%;
      width: 120px;
      text-align: center;
      border-bottom: 2px solid $HpeColorGrey_15;
      @media screen and (max-width: 541px){
        width: 108px;
      }

      a {
        display: flex;
        padding: 0 24px;
        height: 100%;
        justify-content: center;
        align-items: center;


        span {
          @extend %Hpe_Font_Style_02_05;
          @extend %Paragraph_Style_08;
        }

        &.no-activate {
          pointer-events: none;
          cursor: default;
        }
      }

      &:hover,
      &:focus {
        border: 0;
        border-bottom: 2px solid $HpeColorGrey_15;
      }

      &.active {
        border-top: 0;
        color: black;
      }
    }
  }

  .tab-content {
    display: none;
    background: #ccc;
  }

}
