
//radio-buttons

.option-group {


  input[type="radio"] {
    display: none;
  }


  //normal state
  input[type="radio"] + label  {
    @extend %Hpe_Font_Style_02_01;
    span {
     display: inline-block;
     width: 16px;
     height: 16px;
     margin: 0 8px 0px 12px;
     border: 1px solid $HpeColorGrey_05;
     border-radius: 50%;
     background: $HpeColorWhite_01;
     vertical-align: bottom;

       span {
         display:none;
       }
    }
  }

  input[type="radio"]:checked + label  {

    span  {
      border: 1px solid $HpeColorGrey_05;
      span {
        display: block;
        width: 10px;
        height: 10px;
        margin: 2px;
        background: $HpeColorBlue_03;
      }
    }
  }

//disabled state
  input[type="radio"]:disabled + label  {
    @extend %Hpe_Font_Style_02_09;


      span {
       border: 1px solid $HpeColorGrey_10;
       background: $HpeColorGrey_14;
       pointer-events: none;

    }
  }

  input[type="radio"]:disabled:checked + label  {
    span  {
      border: 1px solid $HpeColorGrey_10;
      background: $HpeColorGrey_14;
      pointer-events: none;
      span {

        background: $HpeColorGrey_09;
      }
    }
  }
//invalid state

    input[type="radio"]:invalid + label  {
      @extend %Hpe_Font_Style_02_13;
        span {
         border: 1px solid $HpeColorRed_05;

      }
    }

    input[type="radio"]:invalid:checked + label  {
      span  {
        border: 1px solid $HpeColorRed_05;
        span {

          background: $HpeColorRed_05;
        }
      }
    }


  //hover state
  &:hover {
    input[type="radio"] + label  {
        span {
         border: 1px solid $HpeColorBlue_02;
      }
    }

    input[type="radio"]:checked + label  {
      span  {
        border: 1px solid $HpeColorBlue_02;
        span {

          background: $HpeColorBlue_02;
        }
      }
    }

    //disabled & hover
    input[type="radio"]:disabled + label  {
      @extend %Hpe_Font_Style_02_09;
      pointer-events: none;
        span {
         border: 1px solid $HpeColorGrey_10;
         background: $HpeColorGrey_14;
         pointer-events: none;

      }
    }

    input[type="radio"]:disabled:checked + label  {
      pointer-events: none;
      span  {
        border: 1px solid $HpeColorGrey_10;
        background: $HpeColorGrey_14;
        pointer-events: none;
        span {

          background: $HpeColorGrey_09;
        }
      }
    }

  }

  &:active {

    input[type="radio"] + label  {
        span {
         border: 1px solid $HpeColorBlue_01;
      }
    }

    input[type="radio"]:checked + label  {
      span  {
        border: 1px solid $HpeColorBlue_01;
        span {

          background: $HpeColorBlue_01;
        }
      }
    }
  }
}
