.pills-component {
  .nav-pills {

    li {
      // @include span-columns(1.5 of 16);
      float: right;
      font-size: 14px;
      text-align: center;

      a {
        border-radius: 0;
        color: black;
        padding-bottom: 0;
        padding-left: 0;
        padding-right: 0;


        &:hover,
        &focus {
          background: none;
        }
      }

      &.active {
        a {
          background: transparent;
          border-bottom: 2px solid black;
          color: black;
          text-decoration: none;
        }
      }

    }
  }
}
