.application-updated-modal {
  @extend %Hpe_Layer_Style_01;
  height: 416px;
  width: 444px;
  position: fixed;
  z-index: 1040;
  left: 0;
  top: 15%;
  right: 0;
  margin-left: auto;
  margin-right: auto;

  .modal-header {
    display: none;
  }

  .application-updated-content {
    display: flex;
    flex-direction: column;

    .application-updated-preview {
      height: 196px;
      background-color: $HpeColorGrey_12;
      padding: 20px 0 20px 0;
      display: flex;
      justify-content: center;

      i {
        width: 104px;
        height: 155px;
        box-shadow: 3px 3px 2px rgba(12, 12, 12, 0.15);
      }
    }

    .application-updated-info {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding: 20px 20px 20px 20px;
      height: 171px;
      overflow: auto;

      h1 {
        @extend %Hpe_Font_Style_14_05;
        margin-bottom: 12px;
      }

      .updated-list {
        margin-bottom: 12px;
        flex: 1;

        h2 {
          @extend %Hpe_Font_Style_03_05;
          margin-bottom: 12px;
        }

        ul {
          padding: 0 0 0 24px;

          li {
            @extend %Hpe_Font_Style_03_05;
            margin-bottom: 4px;
            list-style-type: disc;
          }
        }
      }

      h3 {
         @extend %Hpe_Font_Style_03_05;
      }
    }

    .application-updated-footer {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      padding: 7px 20px 7px 20px;
      height: 47px;
      border-top: 1px solid $HpeColorGrey_10;

      button {
        @extend %Button_style_01;
        text-transform: capitalize;
      }
    }
  }

}
