.pending-jobs-component {
  position: relative;
  display: flex;
  justify-content: center;

  .widget {
    height: 100%;
    max-width: 72px;

    h3 {
      @extend %Hpe_Font_Style_01_12;
      text-align: center;
      text-transform: uppercase;
      height: 26px;
    }

    .pending-job-placeholder {
      width: 48px;
      height: 48px;
      margin: auto;
      position: relative;

      .pending-job-number {
        width: 22px;
        height: 22px;
        background-color: $HpeColorBlue_02;
        -moz-border-radius: 50px;
        -webkit-border-radius: 50px;
        border-radius: 50px;
        margin-left: auto;
        display: flex;
        align-items: center;
        justify-content: center;
        border: 2px solid white;
        position: relative;

        span {
          @extend %Hpe_Font_Style_02_03;
          color: white;
        }
      }

      .icon-advice {
        height: 20px;
        width: 20px;
        position: absolute;
        right: -10px;
        top: 25%;
      }
    }
  }


  .small {
    height: 100%;
    max-width: 72px;

    h3 {
      @extend %Hpe_Font_Style_01_12;
      text-align: center;
      text-transform: uppercase;
      height: 26px;
    }

    .pending-job-placeholder {
      width: 48px;
      height: 48px;
      margin: auto;
      position: relative;

      .pending-job-number {
        width: 22px;
        height: 22px;
        background-color: $HpeColorBlue_02;
        -moz-border-radius: 50px;
        -webkit-border-radius: 50px;
        border-radius: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-left: auto;
        border: 2px solid white;
        position: relative;

        span {
          @extend %Hpe_Font_Style_02_03;
          color: white;
        }
      }

      .icon-advice {
        height: 20px;
        width: 20px;
        position: absolute;
        right: -10px;
        top: 25%;
      }
    }
  }

  .large {
    width: 100%;
    height: 172px;

    h3 {
      @extend %Hpe_Font_Style_01_12;
      text-align: center;
      text-transform: uppercase;
      margin-bottom: 16px;
    }

    .pending-jobs-container {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      height: 144px;

      .pending-job-placeholder {
        height: 116px;
        width: 116px;
        position: relative;

        .pending-job-number {
          position: absolute;
          left: 43px;
          top: 50px;
          width: 30px;
          height: 30px;
          background-color: $HpeColorBlue_02;
          -moz-border-radius: 50px;
          -webkit-border-radius: 50px;
          border-radius: 50px;
          margin-left: auto;
          display: flex;
          justify-content: center;
          align-items: center;
          border: 2px solid white;

          span {
            @extend %Hpe_Font_Style_05_03;
            color: white;
          }
        }
      }
    }

    .pending-job-others {
      display: flex;
      flex-direction: row;
      justify-content: space-around;

      .other:first-child {
        margin-right: 10px;
      }

      .other:last-child {
        margin-left: 10px;
      }

      .other {
        display: flex;
        flex-direction: row;
        flex: 1;
        justify-content: center;
        align-items: center;

        .placeholder {
          height: 18px;
          width: 18px;
          position: relative;

          img {
            width: 100%;
            height: 100%;
          }
        }

        .number {
          @extend %Hpe_Font_Style_02_01;
          margin-left: 2px;
        }
      }
    }
  }
}
