.delete-printer-modal {
  @extend %Hpe_Layer_Style_01;
  width: 330px;
  height: 220px;
  position: fixed;
  z-index: 1041;
  left: 0;
  top: 28%;
  right: 0;
  margin-left: auto;
  margin-right: auto;

  .modal-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 7px 20px 7px 20px;
    border-bottom: 1px solid $HpeColorGrey_10;

    h3 {
      @extend %Hpe_Font_Style_03_05;
    }

    .close-modal {
      height: 24px;
      width: 24px;
      display: flex;
      cursor: pointer;
    }
  }

  .modal-content {

    .delete-printer-container {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;

      .delete-printer-form {
        height: 132px;
        display: flex;
        width: 100%;
        padding: 28px 20px 28px 20px;
        border-bottom: 1px solid $HpeColorGrey_10;

        .printer-icon {
          width: 43px;
          height: 30px;
          margin-right: 20px;
          display: flex;
          align-self: center;
          position: relative;

          .icon-status {
            width: 20px;
            height: 20px;
            position: absolute;
            left: 33px;
            bottom: 18px;
          }

        }

        .printer-data {
          flex: 1;
          height: 100%;
          display: flex;
          flex-direction: column;
          justify-content: space-between;

          .form-group {
            display: flex;
            flex-direction: column;

            h3 {
              @extend %Hpe_Font_Style_01_12;
              max-width: 264px;
              text-transform: uppercase;
              margin-bottom: 4px;
            }

            h4 {
              @extend %Hpe_Font_Style_03_05;
              max-width: 225px;
              white-space: nowrap;
              overflow: hidden;
              text-overflow: ellipsis;
              align-items: center;
            }
          }
        }

      }

      .delete-printer-footer {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        padding: 7px 20px 7px 20px;
        width: 100%;
        height: 47px;

        input {
          width: 47%;
          text-align: center;
          cursor: pointer;
          text-transform: capitalize;

          &.cancel-button {
            @extend %Button_style_02;
          }

          &.delete-button {
            @extend %Button_style_01;
          }
        }
      }

    }
  }
}
