.preferences-app-modal {
  @extend %Hpe_Layer_Style_01;
  width: 330px;
  height: 212px;
  position: fixed;
  z-index: 1040;
  left: 0;
  top: 30%;
  right: 0;
  margin-left: auto;
  margin-right: auto;

  .modal-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 7px 20px 7px 20px;
    border-bottom: 1px solid $HpeColorGrey_10;

    h3 {
      @extend %Hpe_Font_Style_03_05;
    }

    .close-modal {
      height: 24px;
      width: 24px;
      display: flex;
      cursor: pointer;
    }
  }

  .preferences-app {
    display: flex;
    flex-direction: column;
    align-items: center;

    .preferences-app-content {
      width: 100%;
      //height: 124px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-around;
      padding: 32px 0 32px 0;

      .form-group-preferences {
        width: 87%;
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom:12px;

        label {
          @extend %Hpe_Font_Style_02_05;
          text-transform: capitalize;
          cursor: default;
        }
        p {
          padding: 5px 12px;
          @extend %Hpe_Font_Style_02_05;
          border:1px solid $HpeColorGrey_08;
          width: 140px;
          border-radius: 0px !important;
          height: 24px;
          -webkit-appearance: none;
          background-image: url("../images/Assets/Icons/Modal/Down_Arrow.svg");
          background-repeat: no-repeat;
          background-position-y: center;
          background-position-x: 92%;
          background-color: transparent;
          position: relative;

          //hover state
          &:hover {
            display:block;
            cursor: pointer;
            border-color: $HpeColorGrey_10;
            background-image: url("../images/Assets/Icons/Modal/Down_Arrow.svg");

          }

          //disabled state
          &:disabled {
            @extend %Hpe_Font_Style_02_05;
            border:1px solid $HpeColorGrey_08;
            background: $HpeColorGrey_14;
          }

          //error state

          &.error {
              border:1px solid $HpeColorRed_03;
              color: $HpeColorRed_03;
          }
        }

        div.border-box {

          max-height:117px;
          position: absolute;
          overflow-y: overlay;
          overflow-x: hidden;
          margin: -1px 0 0;
          width: 140px;
          top: 100%;
          right: 0;
          display: none;
          background: $HpeColorWhite_01;
          border:1px solid $HpeColorGrey_08;
          z-index:2;
          .inner-scroll {
            position:relative;
            width: 140px;
            height: 100%;
          }
        }

        ul {

          width: 140px;
          height: 100%;
          border-radius: 0px !important;
          overflow-y: auto;


          li {
            @extend %Hpe_Font_Style_02_05;
            padding: 8px 24px;
            height:30px;
            border-bottom: 1px solid $HpeColorGrey_12;


          }
          li.active {
            @extend %Hpe_Font_Style_02_04;

            div.icons {

              background-image: url("../images/Assets/Icons/Bar/Large_card_Default@2x.png") !important;
              background-repeat: no-repeat;
              background-color: transparent;
              background-position: center right;
              background-size: 14px 14px;
              background-color: transparent;
              height: 20px;
              width: 25px;
              float: right;
              margin-right: -14px;
              margin-top: -3px;
            }

            &:hover, &:active {
                div.icons {
                background-image: url("../images/Assets/Icons/Bar/Large_card_Default@2x.png") !important;
                background-repeat: no-repeat;
                background-color: transparent;
                background-position: center right;
                background-size: 14px 14px;
                background-color: transparent;
              }
            }
          }

        }

      //close list
        .border-box.not-visible {
          display: none !important;
        }
        //list visible
        .border-box.is-visible {
          display: block;

          ul {
            li:hover {
              @extend %Hpe_Font_Style_02_03;
              background: $HpeColorBlue_02;
              cursor:pointer;
            }
            li:active {
              @extend %Hpe_Font_Style_02_03;
              background: $HpeColorBlue_01;
            }
          }

        }
      }
    }


    .preferences-app-footer {
      width: 100%;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      padding: 7px 20px 7px 20px;
      border-top: 1px solid $HpeColorGrey_10;
      margin-top:20px;

      input {
        text-align: center;
        cursor: pointer;
        text-transform: capitalize;
        color: white;

        &.cancel-button {
          @extend %Button_style_02;
        }

        &.save-button {
          @extend %Button_style_01;
        }
      }
    }
  }
}
