/* Styling for the ngProgress component itself */

.fixed-progress {
  #downloadProgress, #ngProgress {
    position: absolute;

    .inner-progress {
      width:100%;
      height:100%;
      position:relative;
      background-color: $HpeColorBlue_03;
    }
  }
}

/* ngProgress in card component -> printing status */

.card-progress-bar {
  &.printing {
    .animated-progress {
      @include animated-progress;
    }
  }
}
